<template> 
<div>
    <v-card id="scoreTab" :elevation="3">

        <v-container>
            <v-row class="ma-sm-0" :justify="'end'">

                <v-btn class="mx-2 " title="Add Final Contigency" v-if="riskItem.final_contigency==null && riskItem.editPerm && $can('Update RiskContigency')" @click="dialog=true" :top="true" :right="true" fab dark color="indigo">
                    <v-icon dark>fas fa-plus</v-icon>
                </v-btn>

                <v-btn class="mx-2 " title="Add Final Contigency" v-if="riskItem.final_contigency!=null && riskItem.editPerm && $can('Update RiskContigency')" @click="edit()" :top="true" :right="true" fab dark color="amber">
                    <v-icon dark>fas fa-pencil-alt</v-icon>
                </v-btn>

            </v-row>
            <v-row no-gutters class="ma-sm-3" :justify="'space-around'">
                <v-col :sm="12" cols="12" :md="4">
                    <v-card>

                        <v-row class="ma-sm-0">
                            <v-col :sm="3" style="background-color: #fff7f7;align-content: center;display: flex;justify-content: center;">
                                <v-icon class="pa-sm-3" :color="'primary'" style="font-size:78px">fas fa-rupee-sign</v-icon>

                            </v-col>
                            <v-col :sm="9" class="pa-md-2">
                                <v-row no-gutters class="fill-height ">
                                    <v-col class=" text-center">

                                        <span class="d-block headline px-md-2"> Contingency

                                        </span>
                                        <div class="text-subtitle-1">(Initial)</div>
                                        <v-divider></v-divider>
                                        <span class="d-block headline pa-md-2">{{riskItem.initial_contigency}}</span>
                                    </v-col>

                                </v-row>
                            </v-col>

                        </v-row>

                    </v-card>
                </v-col>
                <v-col v-if="riskItem.final_contigency!=null" :sm="12" cols="12" :md="4">
                    <v-card :loading="$store.state.isLoading">

                        <v-row class="ma-sm-0">

                            <v-col :sm="3" style="background-color: #fff7f7;align-content: center;display: flex;justify-content: center;">
                                <v-icon class="pa-sm-3" :color="'primary'" style="font-size:78px">fas fa-rupee-sign</v-icon>

                            </v-col>
                            <v-col :sm="9" class="pa-md-2">
                                <v-row no-gutters class="fill-height ">
                                    <v-col class=" text-center">

                                        <span class="d-block headline px-md-2"> Expenditure

                                        </span>
                                        <div class="text-subtitle-1">(Final)</div>
                                        <v-divider></v-divider>
                                        <span class="d-block headline pa-md-2">{{riskItem.final_contigency}}</span>
                                       
                                    </v-col>

                                </v-row>
                            </v-col>
                        </v-row>
                    </v-card>
                </v-col>
            </v-row>
            <v-row :justify="'center'">
                <v-col v-if="riskItem.final_contigency!=null" :sm="12" cols="12" :md="4">
                    <v-card :loading="$store.state.isLoading">

                        <v-row class="ma-sm-0">

                            <v-col :sm="3" style="background-color: #fff7f7;align-content: center;display: flex;justify-content: center;">
                                <v-icon class="pa-sm-3" :color="riskItem.saving>0?'success':'error'" style="font-size:78px">fas fa-rupee-sign</v-icon>

                            </v-col>
                            <v-col :sm="9" class="pa-md-2">
                                <v-row no-gutters class="fill-height ">
                                    <v-col class=" text-center">

                                        <span class="d-block headline px-md-2"> Savings

                                        </span>
                                        <!-- <div class="text-subtitle-1">(Remaining)</div> -->
                                        <v-divider></v-divider>
                                        <span class="d-block headline pa-md-2">{{riskItem.saving}}</span>
                                    </v-col>

                                </v-row>
                            </v-col>
                        </v-row>
                    </v-card>
                </v-col>
            </v-row>
        </v-container>

    </v-card>

    <v-dialog v-model="dialog" max-width="400">
        <v-card>
            <v-card-title class="headline">Update Final Contingency</v-card-title>

            <v-card-text>
                <v-text-field type="'number'" v-model="contigency" :error-messages="contigencyErrors" label="Contigency Allocated*" required @input="numWords()" @blur="$v.contigency.$touch()"></v-text-field>
                {{numWords().toUpperCase()}}
            </v-card-text>

            <v-card-actions>
                <v-spacer></v-spacer>

                <v-btn color="green darken-1" text @click="dialog = false">
                    Close
                </v-btn>

                <v-btn color="green darken-1" :disabled="$v.$invalid" text @click.once="updateContigency()">
                    Agree
                </v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</div>
</template>

<style scoped>
#scoreTab .v-btn--example {
    top: 10px;
    right: 5%;
    position: absolute;
    margin: 0 0 16px 16px;
    z-index: 1234;
}
</style>

<script>
import {
    required,
    numeric
} from 'vuelidate/lib/validators'
import {digits} from "../../../num";
export default {
    props: {
        riskItem: {
            type: Object,
            required: true
        },
        item_id: {
            type: String,
        }
    },
    data: () => ({
        dialog: false,
        contigency: '',

    }),
    validations: {
        contigency: {
            required,
            numeric

        }
    },
    computed: {
        contigencyErrors() {
            const errors = [];
            if (!this.$v.contigency.$dirty) return errors;
            !this.$v.contigency.numeric && errors.push('Contigency should be numeric.');
            !this.$v.contigency.required && errors.push('Contigency is required.');

            return errors
        },
        finalVal() {
            var result = Number(this.riskItem.final_contigency) - Number(this.riskItem.initial_contigency);
            return result;
        }

    },

    methods: {
        numWords(){
            //this.$v.contigency.$touch()
            var data=digits.numWords(this.contigency);
            return data;
        },

        updateContigency() {
            if (this.$v.$invalid || this.$store.state.isLoading) {
                return;
            }
            const formData = {};
            formData._token = document.getElementById('csrf').content;
            formData.id = this.item_id;
            formData.final = this.contigency;
            this.$store.state.isLoading = true;
            this.$http.post(window.links.updateContigency, formData).then(res => {
                    if (res.status == 200) {
                        this.$store.commit('snackNotify', {
                            type: 'success',
                            msg: res.data.message
                        })

                    }
                })
                .catch(err => {
                    console.log('Error');
                    this.$store.commit('errorFunction', err);

                }).then(() => {
                    this.$store.state.isLoading = false;
                    this.dialog = false;
                    this.$emit('refresh');
                })
        },
        edit(){
            this.contigency=this.riskItem.final_contigency;
            this.dialog=true;
        }

    },

}
</script>
