<template>
<div>
    <v-card id="scoreTab" :elevation="3">

        <v-container fluid>
            <v-dialog v-model="formdialog" persistent max-width="600px">
                <template v-slot:activator="{ on }">
                    <v-btn :color="'primary'" v-if="$can('Update RiskScore')&& riskItem.editPerm" v-on="on" fab small top right @click="setDefault()" class="v-btn--example">
                        <v-icon>fas fa-pencil-alt</v-icon>
                    </v-btn>
                </template>
                <v-card>
                    <v-card-title>
                        <span class="headline">Risk Score</span>
                    </v-card-title>
                    <v-card-text>
                        <v-container>
                            <v-row class="m-0" min-height=200>
                                <v-col cols="12" class="ml-2" md="12">
                                    <div>Initial Likelihood*</div>
                                    <v-slider :tick-labels="slideVal" v-model="likelihood" :track-fill-color="ltcolor" min=1 max=5 ticks="always" tick-size="6" @end="lmyval()"></v-slider>
                                </v-col>

                                <v-col cols="12" class="ml-2" md="12">
                                    <div>Initial Impact*</div>
                                    <v-slider :tick-labels="slideVal" v-model="impact" :track-fill-color="itcolor" min=1 max=5 ticks="always" tick-size="6" @end="imyval()"></v-slider>
                                </v-col>
                                <v-divider></v-divider>
                                <v-col cols="12" class="ml-2" md="12">
                                    <div>Residual Likelihood*</div>
                                    <v-slider :tick-labels="slideVal" v-model="r_likelihood" :track-fill-color="r_ltcolor" min=1 max=5 ticks="always" tick-size="6" @end="r_lmyval()"></v-slider>
                                </v-col>

                                <v-col cols="12" class="ml-2" md="12">
                                    <div>Residual Impact*</div>
                                    <v-slider :tick-labels="slideVal" v-model="r_impact" :track-fill-color="r_itcolor" min=1 max=5 ticks="always" tick-size="6" @end="r_imyval()"></v-slider>
                                </v-col>

                            </v-row>
                        </v-container>
                        <small>*indicates required field</small>
                    </v-card-text>
                    <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn color="blue darken-1" text @click="formdialog = false">Close</v-btn>
                        <v-btn color="blue darken-1" text @click="formdialog = updateScore()">Save</v-btn>
                    </v-card-actions>
                </v-card>
            </v-dialog>
            <v-row class="mt-4 ml-3 p-3" :justify="'space-around'">

                <v-col cols="12" :md="4">
                    <v-card class="pb-4">

                        <v-card-title elevation-4 class="text-center" style="justify-content: center;">
                            Initial Score
                        </v-card-title>

                        <v-row :justify="'center'">
                            <v-col style="text-align: center;">
                                <v-progress-circular :rotate="'270'" :size="'200'" :value="Number(riskItem.score)/.25" :width="'24'" style="font-size:50px;" :color="progressColor()">{{ riskItem.score }}</v-progress-circular>
                            </v-col>
                        </v-row>
                        <v-row :justify="'center'" class="m-2 p-2">
                            <v-col>
                                <div class="title" style="text-align: center;">Likelihood</div>
                                <v-divider></v-divider>
                                <div class="title" style="text-align: center;">{{slideVal[Number(this.riskItem.likelihood)-1]}}</div>
                            </v-col>
                            <v-divider vertical></v-divider>
                            <v-col>
                                <div class="title" style="text-align: center;">Impact</div>
                                <v-divider></v-divider>
                                <div class="title" style="text-align: center;">{{slideVal[Number(this.riskItem.impact)-1]}}</div>
                            </v-col>
                        </v-row>

                    </v-card>
                </v-col>
                <!--  -->
                <v-col cols="12 " v-if="riskItem.r_score" :md="4">
                    <v-card class="pb-4">

                        <v-card-title elevation-4 class="text-center" style="justify-content: center;">
                            Final Score
                        </v-card-title>

                        <v-row :justify="'center'">
                            <v-col style="text-align: center;">
                                <v-progress-circular :rotate="'270'" :size="'200'" :value="Number(riskItem.r_score)/.25" :width="'24'" style="font-size:50px;" :color="progressColor()">{{ riskItem.r_score }}</v-progress-circular>
                            </v-col>
                        </v-row>
                        <v-row :justify="'center'" class="m-2 p-2">
                            <v-col>
                                <div class="title" style="text-align: center;">Likelihood</div>
                                <v-divider></v-divider>
                                <div class="title" style="text-align: center;">{{slideVal[Number(this.riskItem.r_likelihood)-1]}}</div>
                            </v-col>
                            <v-divider vertical></v-divider>
                            <v-col>
                                <div class="title" style="text-align: center;">Impact</div>
                                <v-divider></v-divider>
                                <div class="title" style="text-align: center;">{{slideVal[Number(this.riskItem.r_impact)-1]}}</div>
                            </v-col>
                        </v-row>

                    </v-card>

                </v-col>
            </v-row>
        </v-container>

    </v-card>
</div>
</template>

<style scoped>
#scoreTab .v-btn--example {
    top: 10px;
    right: 5%;
    position: absolute;
    margin: 0 0 16px 16px;
    z-index: 1234;
}
</style>

<script>
export default {
    props: {
        riskItem: {
            type: Object,
            required: true
        },
        item_id: {
            type: String,
        }
    },
    data: () => ({
        formdialog: false,
        slideVal: ['Very Low', 'Low', 'Medium', 'High', 'Very High'],
        likelihood: 1,
        impact: 1,
        score: 1,
        r_likelihood: 1,
        r_impact: 1,
        r_score: 1,
        ltcolor: '',
        itcolor: '',
        r_ltcolor: '',
        r_itcolor: '',

    }),
    methods: {
        progressColor() {

            return this.riskItem.type_id == 2 ? 'red darken-1' : 'teal darken-3';

        },
        setDefault() {

            this.likelihood = this.riskItem.likelihood;
            this.score = this.riskItem.score;
            this.impact = this.riskItem.impact;
            this.r_likelihood = this.riskItem.r_likelihood == false ? 1 : this.riskItem.r_likelihood;
            this.r_score = this.riskItem.r_score == false ? 1 : this.riskItem.r_score;
            this.r_impact = this.riskItem.r_impact == false ? 1 : this.riskItem.r_impact;

        },
        updateScore() {
            const formData = {};
            formData._token = document.getElementById('csrf').content;
            formData.id = this.item_id;
            formData.impact = this.impact;
            formData.likelihood = this.likelihood;
            formData.score = this.score;
            formData.r_score = this.r_score;
            formData.r_impact = this.r_impact;
            formData.r_likelihood = this.r_likelihood;

            this.$http.post(window.links.updateScore, formData).then(res => {
                    if (res.status == 200) {
                        this.$store.commit('snackNotify',{type:'success',msg:res.data.message})
                        this.formdialog = false;

                    }

                    this.$emit('refresh');

                })
                .catch(err => {
                    this.$store.commit('errorFunction', err);

                })
        },
        lmyval() {
            this.ltcolor = '';
            if (this.likelihood > 3) {
                this.ltcolor = 'red';
            }
            this.score = this.likelihood * this.impact
        },
        imyval() {
            this.itcolor = '';
            if (this.impact > 3) {
                this.itcolor = 'red';
            }
            this.score = this.likelihood * this.impact
        },
        r_lmyval() {
            this.r_ltcolor = '';
            if (this.r_likelihood > 3) {
                this.r_ltcolor = 'red';
            }
            this.r_score = this.r_likelihood * this.r_impact

        },
        r_imyval() {
            this.r_itcolor = '';
            if (this.r_impact > 3) {
                this.r_itcolor = 'red';
            }
            this.r_score = this.r_likelihood * this.r_impact
        }
    },

}
</script>
