<template>
<div class="font">
    <v-row>
        <v-col :md="3" :sm="12">
            <div class="display-1 font"> RMS Dashboard </div>
        </v-col>
        <v-col :md="4" min-width="300" :sm="12" offset-md="4">
            <v-select :min-width="300" :items="buList" item-text="name" item-value="id" label="Business List" solo v-model="bu" @change='buChange()' height="30"></v-select>

        </v-col>
        <v-col :md="1" min-width="50" :sm="0" class=".d-none d-md-flex">
            <v-btn @click="snap()" icon color="primary">
                <v-icon>fas fa-camera</v-icon>
            </v-btn>
        </v-col>
    </v-row>
    <!-- class="d-none" -->

    <div v-if="dash!=''" ref="printMe">
        <v-row class="fill-height" max-height="380" style="justify-content:space-around">

            <v-col :sm="12" cols="12" :md="4" :lg="4" class="text-center" style="max-height:380x">
                <v-card class="fill-height hgrad" :elevation="4" style="max-height:380x">
                    <v-card-title class="  py-sm-2 pb-xs-0 font">

                        <v-icon class="mr-sm-3" :color="'warning'">fa-chart-line</v-icon>
                        <span class=" ml-md-2 mt-md-1 py-sm-0 font headline">
                            Risk Items
                        </span>
                    </v-card-title>
                    <v-divider dark class="mb-sm-1"></v-divider>

                    <v-card-text class="mt-md-2">
                        <div class="">

                            <div class="text-center font" style="color:white">

                                <v-row :justify-sm="'center'">

                                    <v-col cols="12" :sm="12">
                                        <h2 class="text-left mb-sm-4 mb-md-4">Approved RiskItems</h2>

                                        <v-progress-linear striped :value="Math.ceil((dash.regItems['approved']/dash.regItems['totalItem'])*100) " color="light-green darken-1" height="30">
                                            <template>
                                                <strong class="font">{{ Math.ceil((dash.regItems['approved']/dash.regItems['totalItem'])*100) }}%</strong>
                                            </template>
                                        </v-progress-linear>
                                        <h2 class="mt-sm-3 mt-md-4 text-right">
                                            <animated-number style="font-size: 1rem !important;  font-weight: 600;" :formatValue="formatToPrice" :value="dash.regItems['approved']" :round="0" :duration="1000" />
                                            <span style="font-size: 1rem !important;  font-weight: 600;"> / </span>
                                            <animated-number style="font-size: 1rem !important;  font-weight: 600;" :formatValue="formatToPrice" :value="dash.regItems['totalItem']" :round="0" :duration="700" />
                                        </h2>

                                    </v-col>

                                </v-row>

                            
                                <v-row :justify="'center'">
                                    <v-col cols="auto">

                                        <v-icon large  class="mt-sm-n1" :color="'white'">fas fa-book</v-icon>
                                        
                                    </v-col>
                                    <v-col cols="auto">
                                        <h1>
                                            <animated-number style="font-size: 3rem !important;  font-weight: 600;" :formatValue="formatToPrice" :value="dash.regItems['regCount']" :round="0" :duration="700" />
                                        </h1>
                                    </v-col>
                                </v-row>

                            </div>

                        </div>

                    </v-card-text>

                </v-card>
            </v-col>
            <v-col :sm="12" cols="12" :md="4" :lg="4" class="text-center" style="max-height:380x">
                <v-card class="fill-height bgi" :elevation="4" style="max-height:380x">
                    <v-card-title class="hgrad py-sm-2 pb-xs-0 font">
                        <v-icon :color="'warning'">fas fa-chart-pie</v-icon>
                        <span class=" ml-md-2 mt-md-1 py-sm-0 font headline">

                            O/T Ratio

                        </span>
                        <v-spacer></v-spacer>
                        <v-switch class="mt-md-1" style="height:30px;" v-model="switchDonut" @change="switchDonutFn()" color="red" :label="''"></v-switch>
                    </v-card-title>
                    <v-card-text class="mt-md-2">
                        <div class="text-center">{{switchDonutTitle}}</div>
                        <apex-chart width="100%" height="200px" type="donut" :options="donutOptions" :series="donut_data"></apex-chart>
                    </v-card-text>

                </v-card>
            </v-col>
            <v-col :sm="12" cols="12" :md="4" :lg="4" data-html2canvas-ignore="true" class="text-center" style="max-height:380x">
                <v-card class="fill-height " :elevation="4" style="max-height:380x">
                    <v-card-title class="hgrad py-sm-2 pb-xs-0 font">
                        <v-icon :color="'warning'">fas fa-chart-line</v-icon>
                        <span class=" ml-md-2 mt-md-1 py-sm-0 font headline">
                            Distribution
                        </span>
                        <v-spacer></v-spacer>
                        <v-switch class="mt-md-1" style="height:30px;" v-model="dswitch" @change="switchDist()" color="red" :label="''"></v-switch>
                    </v-card-title>
                    <v-card-text class="pb-sm-0">
                        <v-carousel hide-delimiters cycle height="100%" hide-delimiter-background show-arrows-on-hover>
                            <v-carousel-item>
                                
                                    <v-row class="fill-height" align="center" justify="center">
                                        <v-card class="fill-height wgi" width="100%" :elevation="0" style="max-height:380x;">
                                            <v-card-title class='py-sm-3 indigo--text  text-center' >
                                                {{dname}} Initial score
                                            </v-card-title>
                                            <v-card-text class="pa-sm-0 text-center">
                                                <v-sparkline style="background-color:transparent" :fill="true" height="80" :gradient="['rgba(255,0,0,1)', 'rgba(246,165,126,1)', 'rgba(249,206,21,1)','rgba(4,255,0,1)','rgba(85,177,84,1)']" :line-width="2" :padding="8" :smooth="14" :value="idist" :gradient-direction="dgrad" auto-draw></v-sparkline>
                                            </v-card-text>

                                        </v-card>
                                    </v-row>
                                
                            </v-carousel-item>
                            <v-carousel-item>
                                
                                    <v-row class="fill-height" align="center" justify="center">
                                        <v-card class="fill-height wgi mt-sm-2" width="100%" :elevation="0" style="max-height:380x">
                                            <v-card-title class='py-sm-3 indigo--text  text-center'>
                                                {{dname}} Residual score
                                            </v-card-title>
                                            <v-card-text class="pa-sm-0 text-center ">
                                                
                                                    <v-sparkline style="background-color:transparent" :fill="true" height="80" :gradient="['rgba(255,0,0,1)', 'rgba(246,165,126,1)', 'rgba(249,206,21,1)','rgba(4,255,0,1)','rgba(85,177,84,1)']" :line-width="2" :padding="8" :smooth="14" :value="rdist" :gradient-direction="dgrad" auto-draw></v-sparkline>

                                                
                                            </v-card-text>

                                        </v-card>
                                    </v-row>
                                
                            </v-carousel-item>
                        </v-carousel>
                    </v-card-text>
                </v-card>

            </v-col>

        </v-row>

        <v-row class="fill-height" style="justify-content:center">
            <v-col :sm="12" cols="12" :md="4" class="text-center fill-height">

                <v-card class="fill-height font hgrad">
                    <v-card-title class="font pb-sm-0" primary-title>
                        <v-icon class="mr-sm-3" :color="'warning'">fas fa-rupee-sign</v-icon>
                        Cost Analysis
                    </v-card-title>
                    <v-card-text class="pb-sm-0">
                        <v-carousel hide-delimiters cycle height="100%" hide-delimiter-background show-arrows-on-hover>
                            <v-carousel-item>
                                <v-sheet style="background-color:transparent" height="100%">
                                    <v-row class="fill-height" align="center" justify="center">
                                        <v-col cols="12" :sm="3">
                                            <v-icon class="pa-sm-3 blue--text text--lighten-3" style="font-size:78px">fas fa-rupee-sign</v-icon>
                                        </v-col>
                                        <v-col cols="12" :sm="9">
                                            <span class="d-block headline font fcolor px-md-2"> Contingency

                                            </span>
                                            <v-divider dark></v-divider>
                                            <span class="d-block headline font fcolor pa-md-2">{{Number(dash.contigency[0]).toLocaleString('en-IN')}}</span>
                                        </v-col>
                                    </v-row>
                                </v-sheet>
                            </v-carousel-item>
                            <v-carousel-item>
                                <v-sheet style="background-color:transparent" height="100%">
                                    <v-row class="fill-height" align="center" justify="center">
                                        <v-col cols="12" :sm="6">
                                            <v-icon class="pa-sm-3 blue--text text--lighten-3" style="font-size:78px">fas fa-rupee-sign</v-icon>
                                        </v-col>
                                        <v-col cols="12" :sm="6">
                                            <span class="d-block font headline fcolor px-md-2">{{Number(dash.final_contigency[0])>0?"Saving":"Loss"}}

                                            </span>

                                            <v-divider dark></v-divider>
                                            <span class="d-block font headline fcolor pa-md-2">
                                                <v-icon large color="red" v-if="Number(dash.final_contigency[0])<0">fas fa-caret-down</v-icon>
                                                <v-icon color="green" v-if="Number(dash.final_contigency[0])>0">fas fa-caret-up</v-icon>
                                                {{Number(dash.final_contigency[0]).toLocaleString('en-IN')}}
                                            </span>
                                        </v-col>
                                    </v-row>
                                </v-sheet>
                            </v-carousel-item>
                        </v-carousel>

                    </v-card-text>

                </v-card>
            </v-col>
            <v-col :sm="12" cols="12" :md="4" class="text-center fill-height">

                <v-card class="fill-height font hgrad">
                    <v-card-title class="font pb-sm-0" primary-title>
                        <v-icon class="mr-sm-3" :color="'warning'">fas fa-rupee-sign</v-icon>
                        Threat
                    </v-card-title>
                    <v-card-text class="pb-sm-0">
                        <v-carousel hide-delimiters cycle height="100%" hide-delimiter-background show-arrows-on-hover>
                            <v-carousel-item>
                                <v-sheet style="background-color:transparent" height="100%">
                                    <v-row class="fill-height" align="center" justify="center">
                                        <v-col cols="12" :sm="3">
                                            <v-icon class="pa-sm-3 red--text text--darken-1" style="font-size:78px">fas fa-rupee-sign</v-icon>
                                        </v-col>
                                        <v-col cols="12" :sm="9">
                                            <span class="d-block headline font fcolor px-md-2"> Contingency

                                            </span>
                                            <v-divider dark></v-divider>
                                            <span class="d-block headline font fcolor pa-md-2">{{Number(dash.contigency[2]).toLocaleString('en-IN')}}</span>
                                        </v-col>
                                    </v-row>
                                </v-sheet>
                            </v-carousel-item>
                            <v-carousel-item>
                                <v-sheet style="background-color:transparent" height="100%">
                                    <v-row class="fill-height" align="center" justify="center">
                                        <v-col cols="12" :sm="6">
                                            <v-icon class="pa-sm-3 red--text text--darken-1" style="font-size:78px">fas fa-rupee-sign</v-icon>
                                        </v-col>
                                        <v-col cols="12" :sm="6">
                                            <span class="d-block font headline fcolor px-md-2"> Final

                                            </span>

                                            <v-divider dark></v-divider>
                                            <span class="d-block font headline fcolor pa-md-2">{{Number(dash.final_contigency[2]).toLocaleString('en-IN')}}</span>
                                        </v-col>
                                    </v-row>
                                </v-sheet>
                            </v-carousel-item>
                        </v-carousel>

                    </v-card-text>

                </v-card>
            </v-col>
            <v-col :sm="12" cols="12" :md="4" class="text-center fill-height">

                <v-card class="fill-height font hgrad">
                    <v-card-title class="font pb-sm-0" primary-title>
                        <v-icon class="mr-sm-3" :color="'warning'">fas fa-rupee-sign</v-icon>
                        Opportunity
                    </v-card-title>
                    <v-card-text class="pb-sm-0">
                        <v-carousel hide-delimiters cycle height="100%" hide-delimiter-background show-arrows-on-hover>
                            <v-carousel-item>
                                <v-sheet style="background-color:transparent" height="100%">
                                    <v-row class="fill-height" align="center" justify="center">
                                        <v-col cols="12" :sm="3">
                                            <v-icon class="pa-sm-3 teal--text text--lighten-1" style="font-size:78px">fas fa-rupee-sign</v-icon>
                                        </v-col>
                                        <v-col cols="12" :sm="9">
                                            <span class="d-block headline font fcolor px-md-2"> Contingency

                                            </span>
                                            <v-divider dark></v-divider>
                                            <span class="d-block headline font fcolor pa-md-2">{{Number(dash.contigency[1]).toLocaleString('en-IN')}}</span>
                                        </v-col>
                                    </v-row>
                                </v-sheet>
                            </v-carousel-item>
                            <v-carousel-item>
                                <v-sheet style="background-color:transparent" height="100%">
                                    <v-row class="fill-height" align="center" justify="center">
                                        <v-col cols="12" :sm="6">
                                            <v-icon class="pa-sm-3 teal--text text--lighten-1" style="font-size:78px">fas fa-rupee-sign</v-icon>
                                        </v-col>
                                        <v-col cols="12" :sm="6">
                                            <span class="d-block font headline fcolor px-md-2"> Final

                                            </span>

                                            <v-divider dark></v-divider>
                                            <span class="d-block font headline fcolor pa-md-2">{{Number(dash.final_contigency[1]).toLocaleString('en-IN')}}</span>
                                        </v-col>
                                    </v-row>
                                </v-sheet>
                            </v-carousel-item>
                        </v-carousel>

                    </v-card-text>

                </v-card>
            </v-col>

        </v-row>

        <v-row class="fill-height" max-height="300">
            <v-col cols="12" :sm="12" :md="4" class="text-center " style="max-height:400px">
                <v-card class="fill-height cgrad" :elevation="4" style="max-height:380x">
                    <v-card-title class="pb-xs-0 font">
                        <v-icon :color="'warning'">fas fa-chart-pie</v-icon>
                        <span class="ml-md-2 mt-md-1 font headline  ">
                            Risk Items by Status
                        </span>
                    </v-card-title>
                    <v-divider class="m-0"></v-divider>
                    <v-card-text class="fill-height">

                        <apex-chart type="bar" max-height="300" height="250" width="100%" :options="chartOptions" v-if="statusChart.count.data.length>0" :series="[statusChart.count]"></apex-chart>

                    </v-card-text>

                </v-card>
            </v-col>

            <v-col cols="12" :sm="12" :md="4" class="text-center " style="max-height:400px">
                <v-card class="fill-height " :elevation="4" style="max-height:380x">
                    <v-card-title class="hgrad pb-xs-0 font">
                        <v-icon :color="'warning'">fas fa-chart-pie</v-icon>
                        <span class="ml-md-2 mt-md-1 font headline  ">
                            RBS Items
                        </span>
                    </v-card-title>
                    <v-divider class="m-0"></v-divider>
                    <v-card-text class="fill-height">
                        <apex-chart width="100%" height="250px" max-height="300" type="pie" :options="rbsOptions" :series="rbsChart"></apex-chart>

                    </v-card-text>

                </v-card>
            </v-col>

            <v-col cols="12" :sm="12" :md="4" class="text-center " style="max-height:400px">
                <v-card class="fill-height " :elevation="4" style="max-height:380x">
                    <v-card-title class="hgrad pb-xs-0 font">
                        <v-icon :color="'warning'">fas fa-chart-pie</v-icon>
                        <span class="ml-md-2 mt-md-1 font headline  ">
                            Actions Completed
                        </span>
                    </v-card-title>
                    <v-divider class="m-0"></v-divider>
                    <v-card-text :align-md="'center'">
                        <v-progress-circular :rotate="270" :size="250" :width="35" :value="actionVal" color="teal">
                            <span class="display-2"> {{actionVal}} % </span>
                        </v-progress-circular>
                    </v-card-text>

                </v-card>
            </v-col>
        </v-row>

        <v-row class="fill-height" :justify="'center'">
            <v-col cols="12" :sm="12" :md="10" class="text-center ">
                <v-card class="fill-height cgrad" :elevation="4">
                    <v-card-title class="pb-xs-0 font">
                        <v-icon :color="'warning'">fas fa-table</v-icon>
                        <span class="ml-md-2 mt-md-1 font headline  ">
                            Project Details
                        </span>
                    </v-card-title>
                    <v-divider class="m-0"></v-divider>
                    <v-card-text class="tbl-card fill-height">
                        <v-data-table dark style="background-color:transparent;color:white" :max-height="400" :headers="projectHeaders" :items="dash.projects" :sort-by.sync="sortBy" :sort-desc="true" :items-per-page="5" class="elevation-1"></v-data-table>
                    </v-card-text>

                </v-card>
            </v-col>

        </v-row>

    </div>

    <v-dialog v-model="dialog" max-width="290">
        <v-card>
            <v-card-title class="headline">Download Snapshot..</v-card-title>

            <v-card-text class="text-center">
                <a ref="clickMe" id="clickMe" :href="snapUrl" download="dashboard.png">
                    <v-btn color="success">
                        <v-icon>fas fa-download</v-icon>
                    </v-btn>
                </a>

            </v-card-text>

        </v-card>
    </v-dialog>

</div>
</template>

<style scoped>
.header {
    font-size: 16px !important;
    color: white !important;

}

.font {
    font-family: 'Nunito', sans-serif !important;

}

.bgi {
    background-color: #f9f9f9
}
.wgi{
    background-color: #ffffff
}

.rgrad {
    background-color: #f7b42c;
    background-image: linear-gradient(315deg, #f7b42c 0%, #fc575e 74%);
    color: white !important;
}

.hgrad {
    background-color: #a4508b;
    background-image: linear-gradient(326deg, #a4508b 0%, #5f0a87 74%);

    color: white !important;
}

.bgrad {
    background-color: #7f53ac;
    background-image: linear-gradient(315deg, #7f53ac 0%, #647dee 74%);
    color: white !important;
}

.cgrad {
    background-color: #a4508b;
    background-image: linear-gradient(326deg, #a4508b 0%, #000000 74%);
    color: white !important;
}

.itxt {
    font-weight: 400;
    font: Nunito;
    font-size: 2.125;
}

.fcolor {
    color: white !important;
}


</style>

<script>
import AnimatedNumber from "animated-number-vue";
export default {

    data: () => ({
        dataLoad: true,
        dialog: false,
        snapUrl: '',
        donut_data: '',
        switchDonut: false,
        switchDonutTitle: 'Item count',
        sortBy: 'threat',
        projectHeaders: [{
                text: 'Project',
                align: 'start',
                sortable: false,
                value: 'name',
                class: "grey--text text--lighten-5 title"
            },
            {
                text: 'Register',
                value: 'project',
                class: "grey--text text--lighten-5 title"
            },
            {
                text: 'Items',
                value: 'items',
                class: "grey--text text--lighten-5 title"
            },
            {
                text: 'Contingency(O)',
                value: 'opp',
                class: "grey--text text--lighten-5 title"
            },
            {
                text: 'Contingency(T)',
                value: 'threat',
                class: "grey--text text--lighten-5 title"
            }

        ],
        buList: [],
        bu: '',
        dash: [],

        allData: [],

        data: [],
        dswitch: false,
        dgrad: 'left',
        idist: [],
        rdist: [],
        dname: 'Opportunity',

        actionVal: '',

        donutOptions: {
            chart: {
                id: 'OT-Chart',
                width: "100%",

            },
            labels: ["Opportunity", "Threat"],

            responsive: [{
                breakpoint: 480,
                options: {
                    chart: {
                        width: "100%"
                    },
                    legend: {
                        show: false,
                        //position: 'bottom'
                    }
                }
            }],
            plotOptions: {
                pie: {
                    donut: {
                        size: '50%',
                        labels: {
                            show: true,
                            name: {
                                show: false,
                                formatter: function (val) {
                                    return 'ITEMS'
                                }
                            },
                            value: {
                                show: true,
                                formatter: function (val) {
                                    return val;
                                }
                            }
                        }
                    }
                }
            },

            legend: {
                show: false,
                position: 'bottom'
            },
            dataLabels: {
                enabled: true
            },

            colors: ['#28c76f', '#ea5455']

        },
        chartOptions: {
            chart: {
                type: 'bar',
                height: "100%",
                toolbar: {
                    show: false
                }
            },
            legend: {
                show: false
            },

            responsive: [{
                breakpoint: 480,
                options: {
                    chart: {
                        width: "100%"
                    },

                }
            }],
            plotOptions: {
                bar: {
                    horizontal: true,
                    barHeight: '70%',
                    endingShape: 'flat',
                    distributed: true,
                },

            },
            colors: ['#7367F0', '#28C76F', '#EA5455', '#FF9F43', '#0075cf'],
            dataLabels: {
                enabled: true,
                textAnchor: 'start',
                style: {
                    colors: ['#ffffff'],
                    fontSize: '14px',
                    fontFamily: 'Roboto',
                    fontWeight: 'bold',
                },
                formatter: function (val, {
                    seriesIndex,
                    dataPointIndex,
                    w
                }) {

                    return w.globals.labels[dataPointIndex] + ":  " + val
                },
                offsetX: 0,
                dropShadow: {
                    enabled: true
                }
            },
            stroke: {
                show: false,
                width: 0,
                colors: ['#fff'],

            },
            xaxis: {
                show: false,
                categories: [],
                lines: {
                    show: false,
                },
                labels: {
                    show: false,
                },
                axisBorder: {
                    show: false,
                },
                axisTicks: {
                    show: false,
                }
            },
            yaxis: {
                show: false,
                lines: {
                    show: false,
                },
                labels: {
                    show: false,
                    style: {
                        fontSize: '14px',
                        fontWeight: 600
                    }
                },
                axisBorder: {
                    show: false,
                },
                axisTicks: {
                    show: false,
                }

            },
            grid: {
                xaxis: {
                    lines: {
                        show: false
                    }
                },
                yaxis: {
                    lines: {
                        show: false
                    }
                },
            }

        },
        rbsOptions: {
            chart: {
                width: "100%",
                type: 'pie',
                height: 250,
            },
            legend: {
                show: false,
                horizontalAlign: 'left'
            },
            colors: ["#3366cc", "#dc3912", "#ff9900", "#109618", "#990099", "#0099c6", "#dd4477", "#66aa00", "#b82e2e", "#316395", "#3366cc", "#994499", "#22aa99", "#aaaa11", "#6633cc", "#e67300", "#8b0707", "#651067", "#329262", "#5574a6", "#3b3eac", "#b77322", "#16d620", "#b91383", "#f4359e", "#9c5935", "#a9c413", "#2a778d", "#668d1c", "#bea413", "#0c5922", "#743411"],
            labels: [],
            responsive: [{
                // breakpoint: 480,
                // options: {
                //     chart: {
                //         width: "100%"
                //     },
                //     legend: {
                //         position: 'bottom'
                //     }
                // }
            }]
        },
        statusChart: {
            name: [],
            count: []
        },

    }),
    components: {
        'apexChart': VueApexCharts,
        AnimatedNumber
    },

    methods: {
        formatToPrice(value) {
            return `<span>${Number(value).toFixed(0)}</span>`;
        },
        getData() {

            this.$http.get(window.links.dashboard).then((response) => {
                const dt = response.data;
                this.data = dt.dash;
                this.buList = dt.bu;

            }).catch(
                (error) => {
                    this.$store.commit('snackNotify', {
                        type: 'error',
                        msg: 'Data Fetching Error'
                    })
                }
            ).then(() => {
                const len = this.buList.length - 1;
                this.bu = this.buList[len].id;
                this.buList.forEach(element => {
                    if (element.id != "all") {
                        this.getDashboard(element.id)
                    }
                });

                this.buChange();
                this.$store.state.isLoading = false;

            })

        },
        getDashboard(id) {

            const formData = {};
            formData.id = id;
            formData._token = document.getElementById('csrf').content;
            this.$http.post(window.links.getDashboard, formData).then((response) => {
                this.data[id] = response.data
            }).catch(
                (error) => {
                    this.$store.commit('snackNotify', {
                        type: 'error',
                        msg: 'Data Fetching Error'
                    })
                }
            ).then(() => {

                //
            })

        },

        buChange() {
            this.dash = this.data[this.bu];

            this.switchDist();
            this.statusChartFn(this.dash);
            this.rbsChartFn(this.dash);

            if (this.dash.actions.total == 0) {
                this.actionVal = 0;
            } else {
                this.actionVal = ((Number(this.dash.actions.comp) / Number(this.dash.actions.total)) * 100).toFixed(1);
            }

            this.switchDonutFn();

        },
        occurence(array, n) {

            var result = {};
            if (array instanceof Array) { // Check if input is array.
                array.forEach(function (v, i) {
                    if (!result[v]) { // Initial object property creation.
                        result[v] = [i]; // Create an array for that property.
                    } else { // Same occurrences found.
                        result[v].push(i); // Fill the array.
                    }
                });
            }
            n = n.toString();
            if (result[n]) {
                return result[n].length;
            } else {
                return 0;
            }

        },
        sparkLine(arr) {
            var data = [
                this.occurence(arr, 1),
                this.occurence(arr, 2),
                this.occurence(arr, 3),
                this.occurence(arr, 4),
                this.occurence(arr, 5),
                this.occurence(arr, 6),
                this.occurence(arr, 8),
                this.occurence(arr, 9),
                this.occurence(arr, 10),
                this.occurence(arr, 12),
                this.occurence(arr, 15),
                this.occurence(arr, 16),
                this.occurence(arr, 20),
                this.occurence(arr, 25),

            ];

            return data;
        },
        switchDist() {
            if (this.dswitch) {
                this.idist = this.sparkLine(this.dash.scoreLine.threat_i);
                this.rdist = this.sparkLine(this.dash.scoreLine.threat_r);
                this.dgrad = 'right';
                this.dname = 'Threat';
            } else {
                this.idist = this.sparkLine(this.dash.scoreLine.opp_i);
                this.rdist = this.sparkLine(this.dash.scoreLine.opp_r);
                this.dgrad = 'left';
                this.dname = 'Opportunity';
            }
        },
        switchDonutFn() {
            if (this.switchDonut) {
                var opp = Number(this.dash.contigency[1]);
                var threat = Number(this.dash.contigency[2]);
                var total = opp + threat;
                if (total != 0) {
                    this.donut_data = [+((opp / total) * 100).toFixed(0), +((threat / total) * 100).toFixed(0)];

                } else {
                    this.donut_data = [0, 0];
                }
                this.switchDonutTitle = "Cost-wise"

            } else {
                this.donut_data = this.dash.typeData;
                this.switchDonutTitle = "Item count"
            }
        },
        statusChartFn(n) {
            this.statusChart.count = [];
            this.statusChart.count = {

                data: n.statusReport.map((x) => {
                    return Number(x.count);
                })
            }
            this.chartOptions.xaxis.categories = [];
            this.chartOptions.xaxis.categories = n.statusReport.map((x) => {
                return x.name;
            })
            var maxVal = Math.max(...this.statusChart.count.data)

            var r = 0.5;
            if (maxVal > 30) {
                r = 0.3
            } else if (maxVal > 20) {
                r = 0.4
            } else {
                r = 0.5
            }

            this.chartOptions.yaxis.max = maxVal + (r * maxVal);

        },
        rbsChartFn(n) {
            this.rbsChart = [];
            this.rbsChart =
                n.rbsReport.map((x) => {
                    return Number(x.count);
                })
            this.rbsOptions.labels = [];
            this.rbsOptions.labels = n.rbsReport.map((x) => {
                return x.name;
            })
        },
        async snap() {

            const el = this.$refs.printMe;
            // add option type to get the image version
            // if not provided the promise will return 
            // the canvas.
            const options = {
                type: 'dataURL'
            }
            this.snapUrl = await this.$html2canvas(el, options);
            const ele = this.$refs.clickMe;
            this.dialog = true;
            //document.getElementById("clickMe").click();
            // console.log(ele);
            // ele.click();
            // await html2canvas(el).then((canvas, options)=>{
            //     this.snapUrl=canvas.toDataURL()
            // });
            // console.log(this.snapUrl);
        }
    },
    created() {
        this.$store.state.isLoading = true;
        this.getData();

    }

}
</script>
