<template>
<div>
    <v-card :elevation="3" class="pt-1">
        <v-card-title primary-title>
            File Attachments
            <v-row :justify="'end'" class="m-0 mt-3 mr-3">
            <v-dialog v-model="formdialog" max-width="600px">
                <template v-slot:activator="{ on }">
                    <v-btn :color="'primary'" v-on="on" fab small top right>
                        <v-icon>fas fa-plus</v-icon>
                    </v-btn>
                </template>
                <v-card>
                    <v-card-title>
                        <span class="headline">File Upload</span>
                    </v-card-title>
                    <v-card-text>
                        <v-container grid-list-xs>
                            <v-row>
                                <v-col cols="12" :md="12">
                                    <v-text-field name="name" v-model="name" label="Name of the File"></v-text-field>
                                </v-col>
                                <v-col cols="12" :md="12">
                                    <v-file-input @change="onChangeFileUpload($event)" ref="file" accept="image/png, image/jpeg, image/bmp,application/pdf" placeholder="Upload file" prepend-icon="fas fa-paperclip" label="Upload a file"></v-file-input>
                                </v-col>
                                <v-col cols="12" :md="12" :justify="'center'">
                                    <v-btn :color="'primary'" @click="subForm()">Submit</v-btn>
                                </v-col>
                            </v-row>

                        </v-container>
                    </v-card-text>

                </v-card>
            </v-dialog>
        </v-row>
        </v-card-title>
        

        <v-divider></v-divider>
        <v-row class="pa-md-2" v-if="files.length>0" :justify="'start'">

            <v-col cols=12 :md="3" v-for="(f,k) in files" :key="k" max-width="200">
                <a :href="f.file" target="blank" style="cursor:pointer;text-decoration: none;">
                    <v-card>
                        <v-card-title class="display-2 justify-center">
                            <v-icon x-large v-if="f.extension=='pdf'" :color="'primary'">fas fa-file-pdf</v-icon>
                            <v-icon x-large v-else :color="'primary'">fas fa-file-image</v-icon>
                        </v-card-title>
                        <v-card-text class="text-center">
                            {{f.name}}
                        </v-card-text>
                    </v-card>
                </a>

            </v-col>

        </v-row>
        <v-row v-if="files.length==0" :justify="'center'">
            <v-col cols="12" :md="8" :justify="'center'">
                <v-alert type="error">
                    No Files Found
                </v-alert>
            </v-col>
        </v-row>
    </v-card>

</div>
</template>

<script>
export default {
    props: {
        item_id: {
            type: String
        },
        files: {
            type: Array,
            required: true,
        }
    },
    data: () => ({
        formdialog: false,
        name: '',
        file: '',

    }),
    methods: {
        subForm() {

            if (this.name && this.file) {
                let formData = new FormData();
                formData.append('file', this.file);
                formData.append('name', this.name);
                formData.append('id', this.item_id);

                this.$http.post(window.links.fileUpload, formData, {
                        headers: {
                            "Content-Type": "multipart/form-data"
                        }
                    }).then(res => {
                        this.$store.commit('snackNotify',{type:'success',msg:'File Uploaded'})
                        this.$emit('refresh');
                        this.formdialog = false;
                    })
                    .catch(err => {
                        this.$store.commit('errorFunction', err);

                    })
            } else {
                this.$store.commit('snackNotify',{type:'error',msg:'Fields Missing'})
            }

        },
        onChangeFileUpload(e) {
            this.file = e;
        }
    },
    created() {
        console.log(this.files);
    }
}
</script>
