<template>
<div>
    <v-overlay :value="$store.state.isLoading">
        <v-progress-circular indeterminate size="64"></v-progress-circular>
    </v-overlay>
    <v-snackbar v-model="$store.state.snackbar" :bottom="true" :color="$store.state.snackColor" :timeout="5000">
        <span v-html="$store.state.snackText"></span>
    </v-snackbar>
    
    <v-navigation-drawer v-model="drawer" app>
        <v-list shaped>
            <v-list-item-group v-model="item" color="primary">
                <v-list-item @click="nav(1)">
                    <v-list-item-action>
                        <v-icon>fas fa-home</v-icon>
                    </v-list-item-action>
                    <v-list-item-content>
                        <v-list-item-title>Dashboard</v-list-item-title>
                    </v-list-item-content>

                </v-list-item>
                <v-list-item @click="nav(2)">
                    <v-list-item-action>
                        <v-icon>fas fa-plus</v-icon>
                    </v-list-item-action>
                    <v-list-item-content>
                        <v-list-item-title>Risk Register</v-list-item-title>
                    </v-list-item-content>
                </v-list-item>
            </v-list-item-group>
        </v-list>
    </v-navigation-drawer>

    <v-app-bar app color="indigo" dark>
        <v-app-bar-nav-icon @click.stop="drawer = !drawer"></v-app-bar-nav-icon>
        <v-toolbar-title>SYSTRA - RMS</v-toolbar-title>
        <v-spacer></v-spacer>

        <v-menu offset-y>
            <template v-slot:activator="{ on }">
                <v-btn icon v-on="on">
                    <v-icon>fas fa-user</v-icon>
                </v-btn>
            </template>

            <v-list>
                <v-list-item @click="() => {}">
                    <v-list-item-title>Account</v-list-item-title>
                </v-list-item>
                <v-list-item :href="links.logout">
                    <v-list-item-title>Logout</v-list-item-title>
                </v-list-item>
            </v-list>
        </v-menu>
    </v-app-bar>

</div>
</template>

<script>
export default {
    props: {
        links: {
            type: Object,
            required: true,
        }
    },
    data: () => ({
        drawer: null,
        item: 1,
    }),
    methods: {
        nav(val) {
            if (val == 1) {
                this.$router.push({
                    name: 'Dashboard'
                })
            } else if (val == 2) {
                this.$router.push({
                    name: 'Register'
                })
            }

        }
    }
}
</script>
