import Vue from "vue";
import Vuelidate from "vuelidate";
import Vuetify from "vuetify";

import router from "./router/router.js";
import store from "./store/store.js";
import Permissions from "./mixins/permissions";
import VueHtml2Canvas from "vue-html2canvas";

Vue.use(VueHtml2Canvas);

window.Vue = Vue;
Vue.use(Vuelidate);
Vue.use(Vuetify);
Vue.mixin(Permissions);
Vue.component("rms-header", require("./components/RmsHeader").default);
Vue.component("rms-dashboard", require("./components/Dashboard.vue").default);
Vue.component(
    "rms-register",
    require("./components/register/Register.vue").default
);

import Axios from "./axios";

Vue.prototype.$http = Axios;

const app = new Vue({
    el: "#app",
    router: router,
    store,
    vuetify: new Vuetify({
        icons: {
            iconfont: "fa"
        }
    })
});
