import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        isLoading: false,
        snackbar: false,
        snackColor: "",
        snackText: "",

        buList: [],
        links: {
            assignApprover: "http://localhost:82/rms/assignApprover",
            approveItem: "http://localhost:82/rms/approveItem",
            fileUpload: "http://localhost:82/rms/fileUpload",

            getfile: "http://localhost:82/getfile"
        },
        itemData: {},
        RbsData: [],
        RiskOwnerData: [],
        RiskDetailOverlay: false,
        registers: []
    },
    mutations: {
        errorFunction: (state, err) => {
            let errText = "";

            if (err.status == 422) {
                Object.values(err.data.errors).forEach(val => {
                    errText += val + "<br>";
                });
                state.snackText = errText;
                state.snackColor = "error";
                state.snackbar = true;
            } else {
                console.log(err.response);
                if (err.response.data.message) {
                    state.snackText = err.response.data.message;
                } else {
                    state.snackText = "Something went wrong";
                }

                state.snackColor = "error";
                state.snackbar = true;
            }
        },
        getSingleRegister: (state, id) => {
            axios
                .get(state.getSingleRegister + id)
                .then(response => {
                    const dt = response.data;
                    Object.values(dt).forEach(val => {
                        state.itemData = val;
                    });
                    console.log(dt);
                    console.log(state.itemData);
                    return true;
                })
                .catch(error => {
                    console.log("Register Ajax error logged");
                    return false;
                });
        },

        getRiskOwner() {
            axios
                .get(state.getOwner)
                .then(response => {
                    const dt = response.data;
                    Object.values(dt).forEach(val => {
                        state.RiskOwnerData = val;
                    });

                    return true;
                })
                .catch(error => {
                    console.log("Rbs Ajax error logged");
                    return false;
                });
        },
        snackNotify(state, payload) {
            state.snackColor = payload.type;
            state.snackText = payload.msg;
            state.snackbar = true;
        }
    },
    actions: {
        getRbs(context) {
            axios
                .get(window.links.getRbs)
                .then(response => {
                    const dt = response.data;
                    Object.values(dt).forEach(val => {
                        context.state.RbsData = val;
                    });
                })
                .catch(error => {
                    console.log("Rbs Ajax error logged");
                });
        },
        getOwner(context) {
            axios
                .get(window.links.getOwner)
                .then(response => {
                    const dt = response.data;
                    Object.values(dt).forEach(val => {
                        context.state.RiskOwnerData = val;
                    });
                })
                .catch(error => {
                    console.log("Owner Ajax error logged");
                });
        }
    }
});
