<template>
<div>
    <v-container :fluid="true">

        <v-row :align="'center'" style="margin-top:16px;">
            <span class="d-inine-block display-1">{{register.name}}</span>
            <v-spacer></v-spacer>

            <v-btn class="mx-6" v-show="!dMode" v-if="dashLoaded" @click="dMode=true" fab dark :color="'success'">
                <v-icon dark>fas fa-chart-line</v-icon>
            </v-btn>

            <v-btn class="mx-6" v-show="dMode" @click="dMode=false" fab dark :color="'success'">
                <v-icon dark>fas fa-table</v-icon>
            </v-btn>

            <v-speed-dial v-show="!dMode" style="top:0px !important" v-model="btnGroup" :top="true" :right="true" :direction="'bottom'" :transition="'slide-y-reverse-transition'">
                <template v-slot:activator>
                    <v-btn v-model="btnGroup" color="blue darken-2" dark fab>
                        <v-icon v-if="btnGroup">fas fa-times</v-icon>
                        <v-icon v-else>fas fa-cog</v-icon>
                    </v-btn>
                </template>
                <a :href="register.reportLink" title="Download" :style="{textDecoration:'none'}" v-show="riskItems.length>0">
                    <v-btn class="mx-2" fab dark color="purple">
                        <v-icon dark>fas fa-arrow-alt-circle-down</v-icon>
                    </v-btn>
                </a>

                <v-btn class="mx-2" fab dark v-if="$can('Create RiskItem')" @click.prevent="setDefault()" color="indigo">
                    <v-icon dark>fas fa-plus</v-icon>
                </v-btn>

                <v-btn class="mx-2" v-if="$can('Deactivate RiskRegister')" fab dark color="red" @click="delDialog=true">

                    <v-icon v-if="register.status==1">fas fa-unlink</v-icon>
                    <v-icon v-else>fas fa-link</v-icon>
                </v-btn>
            </v-speed-dial>

        </v-row>

        <v-divider class="mt-3"></v-divider>
        <transition name="fadeUp" enter-active-class="fadeInDown delayIn" leave-active-class="fadeOutUp" tag="div">
            <item-dashboard :key="childKey" v-show="dMode"  v-if="dashLoaded && riskItems.length>0" :register="register"></item-dashboard>
        </transition>
        <transition name="fadeUp" enter-active-class="fadeInUp delayOut" leave-active-class="fadeOutDown" tag="div">
            <v-row :align="'start'" v-show="!dMode" :justify="'center'" v-if="riskItems.length>0">
                <v-col cols="12">
                    <v-card>
                        <v-card-title>
                            Identified Risk Items
                            <v-spacer></v-spacer>
                            <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line hide-details></v-text-field>
                        </v-card-title>
                        <v-card-text>
                            <v-data-table :headers="headers" c :search="search" :items="riskItems" :items-per-page="10" class="elevation-2">
                                <template v-slot:item.action="{ item }">
                                    <div>

                                        <v-btn class="mx-2" fab dark x-small @click="viewItem(item)" color="success">
                                            <v-icon dark>fas fa-search</v-icon>
                                        </v-btn>

                                        <v-btn class="mx-2" v-if="$can('Edit RiskItem') && item.editPerm" fab dark x-small @click="editItem(item)" color="warning">
                                            <v-icon dark>fas fa-pencil-alt</v-icon>
                                        </v-btn>

                                        <v-btn class="mx-2" v-if="$can('Delete RiskItem') && item.editPerm" fab dark x-small @click="delItem(item)" color="error">
                                            <v-icon dark>fas fa-trash</v-icon>
                                        </v-btn>
                                    </div>
                                </template>
                                <template v-slot:item.is_approved="{ item }">
                                    <div>

                                        <v-icon v-if="item.is_approved && item.is_closed==1" title="Item Approved and Closed" :color="'success'">fas fa-check</v-icon>
                                        <v-icon v-else-if="item.is_approved && item.closure_requested" title="Item requested for Closure" :color="'warning'">fas fa-certificate</v-icon>
                                        <v-icon v-else-if="item.is_approved && item.is_reject==1" title="Item requested for Closure and Rejected" :color="'error'">fas fa-exclamation</v-icon>
                                        <v-icon v-else-if="item.is_approved" title="Item has been accepted" :color="'warning'">fas fa-circle</v-icon>
                                        <v-icon v-else-if="item.is_reject==1" title="Item has been rejeted" :color="'warning'">fas fa-times</v-icon>
                                        <v-icon v-else :color="'error'" title="Item created and Pending Approval">fas fa-circle</v-icon>

                                    </div>
                                </template>
                            </v-data-table>
                        </v-card-text>
                    </v-card>
                </v-col>
            </v-row>

        </transition>
        <v-row :justify="'center'">
            <v-col :justify="'center'">
                <v-alert class="mt-6" max-width="800px" :align-center="true" v-if="noItem" type="error">
                    No Items Found
                </v-alert>
            </v-col>

        </v-row>

        <!-- Create Item Form -->
        <v-row justify="center">
            <v-dialog v-model="formdialog" persistent max-width="800px">
                <v-card :loading="formLoader" :disabled="formLoader">
                    <v-toolbar :color="'primary'" :dark="true" shaped>
                        <v-app-bar-nav-icon></v-app-bar-nav-icon>

                        <v-toolbar-title>Add New Risk</v-toolbar-title>

                        <v-spacer></v-spacer>

                        <v-btn icon fab x-small @click="formdialog=false">
                            <v-icon>fas fa-times</v-icon>
                        </v-btn>

                    </v-toolbar>
                    <v-stepper v-model="e6" vertical>
                        <v-stepper-step :complete="e6 > 1" step="1">
                            Basic Details

                        </v-stepper-step>

                        <v-stepper-content step="1">
                            <v-row class="m-0">
                                <v-col cols="12" md="12">
                                    <v-text-field v-model="des" :error-messages="desErrors" :counter="30" label="Short Description*" required @input="$v.des.$touch()" @blur="$v.des.$touch()"></v-text-field>
                                </v-col>

                                <v-col cols="12" md="12">
                                    <v-radio-group row :error-messages="typeErrors" label="Risk Type*" :value="type" v-model="type" required @input="$v.type.$touch()" @blur="$v.type.$touch()">
                                        <v-radio label="Opportunity" value="1"></v-radio>
                                        <v-radio label="Threat" value="2"></v-radio>
                                    </v-radio-group>
                                </v-col>
                                <v-col cols="12" md="12">
                                    <v-autocomplete :items="rbslist" item-text="name" item-value="id" :error-messages="rbsErrors" v-model="rbs" @input="$v.rbs.$touch()" @blur="$v.rbs.$touch()" label="Risk Breakdown Structure*"></v-autocomplete>
                                </v-col>
                                <v-col cols="12" md="12">
                                    <v-autocomplete :items="ownerlist" item-text="name" item-value="id" :error-messages="ownerErrors" v-model="owner" @input="$v.owner.$touch()" @blur="$v.owner.$touch()" label="Risk Owner*"></v-autocomplete>
                                </v-col>

                            </v-row>
                            <v-btn color="primary" :disabled="stepOne" @click="e6 = 2">Continue</v-btn>

                        </v-stepper-content>

                        <v-stepper-step :complete="e6 > 2" step="2">Description of Risk</v-stepper-step>

                        <v-stepper-content step="2">
                            <v-row class="m-0">
                                <v-col cols="12" md="12">
                                    <v-textarea clearable clear-icon="fas fa-times" label="Cause" :error-messages="causeErrors" :counter="300" v-model="cause" outlined @input="$v.cause.$touch()" @blur="$v.cause.$touch()"></v-textarea>
                                </v-col>
                                <v-col cols="12" md="12">
                                    <v-textarea clearable clear-icon="fas fa-times" label="Detail Description" :error-messages="detailErrors" :counter="300" v-model="detail" outlined @input="$v.detail.$touch()" @blur="$v.detail.$touch()"></v-textarea>
                                </v-col>
                                <v-col cols="12" md="12">
                                    <v-textarea clearable clear-icon="fas fa-times" label="Effects" :error-messages="effectErrors" :counter="300" v-model="effect" outlined @input="$v.effect.$touch()" @blur="$v.effect.$touch()"></v-textarea>
                                </v-col>
                                <v-col cols="12" md="12">
                                    <v-text-field type="'number'" v-model="contigency" @input="numWords()" :error-messages="contigencyErrors" label="Contigency Allocated*" required @blur="$v.contigency.$touch()"></v-text-field>
                                </v-col>
                                <v-col cols="12" md="12" class="font-weight-black">
                                    {{numWords().toUpperCase()}}
                                </v-col>
                            </v-row>

                            <v-btn color="primary" :disabled="steptwo" @click="e6 = 3">Continue</v-btn>
                            <v-btn color="info" @click="e6 = 1" text>Previous</v-btn>
                        </v-stepper-content>

                        <v-stepper-step :complete="e6 > 3" step="3">Initial Scoring</v-stepper-step>

                        <v-stepper-content step="3">
                            <v-row class="m-0" min-height=200>
                                <v-col cols="12" class="ml-2" md="12">
                                    <div>Likelihood</div>
                                    <v-slider :tick-labels="slideVal" v-model="likelihood" :track-fill-color="ltcolor" min=1 max=5 ticks="always" tick-size="6"></v-slider>
                                </v-col>
                                <v-divider></v-divider>
                                <v-col cols="12" class="ml-2" md="12">
                                    <div>Impact</div>
                                    <v-slider :tick-labels="slideVal" v-model="impact" :track-fill-color="itcolor" min=1 max=5 ticks="always" tick-size="6"></v-slider>
                                </v-col>
                            </v-row>
                            <v-divider></v-divider>

                            <v-btn color="primary" v-if="formMode=='create'" class="float-right" :disabled="$v.$invalid || formLoader" @click="subForm()">Submit</v-btn>

                            <v-btn color="primary" v-if="formMode=='update'" class="float-right" :disabled="$v.$invalid || formLoader" @click="updateForm()">Update</v-btn>

                            <v-btn class="float-right" color="info" @click="e6 = 2" text>Previous</v-btn>

                        </v-stepper-content>

                    </v-stepper>
                </v-card>
            </v-dialog>
        </v-row>
        <!-- End of Create titrm form -->

        <!-- De Link Register Dialog -->
        <v-row :justify="'center'">
            <v-dialog v-model="delDialog" width="500">

                <v-card>
                    <v-card-title class="headline amber text-light" primary-title>
                        <v-Icon class="mr-2">fas fa-exclamation-triangle</v-Icon>Caution Notice
                    </v-card-title>

                    <v-card-text class="mt-sm-2">
                        This will Link/De-Link all the data of your Risk Register in the Dashboard.
                        Proceed with caution
                    </v-card-text>

                    <v-divider></v-divider>

                    <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn color="error" text @click="deactivateRegister()">
                            <span v-if="register.status==2">Link</span>
                            <span v-else>De-Link</span>
                        </v-btn>
                        <v-btn color="success" text @click="dialog = false">
                            close
                        </v-btn>

                    </v-card-actions>
                </v-card>
            </v-dialog>
        </v-row>
        <!-- End of Delink Register Dialog -->
    </v-container>
</div>
</template>

<style scoped>
.v-data-table-header {
    background-color: #3f51b5 !important;

}

.v-data-table-header>tr th {
    font-size: 16px !important;
    color: white !important;
}

.v-data-table-header__icon {
    color: white !important;
}

.delayIn {
    animation-duration: 2s;
    animation-delay: 0.1s
}

.delayOut {
    animation-duration: 2s;
    animation-delay: 0.1s;
}

.fadeOut {
    animation-duration: 0.1s;
}
</style>

<script>
import {
    required,
    maxLength,
    numeric
} from 'vuelidate/lib/validators'

import ItemsDashboard from './ItemsDashboard.vue';
import {
    digits
} from "../../num";

export default {
    data: () => ({
        dashLoaded:false,
        delDialog: false,
        dMode: false,
        noItem: false,
        e6: 1,
        panel: false,
        formdialog: false,
        formLoader: false,

        btnGroup: false,

        formid: '',
        type: '',
        rbs: '',
        owner: '',
        des: '',
        cause: '',
        detail: '',
        effect: '',
        contigency: '',
        likelihood: 1,
        impact: 1,
        score: 1,
        slideVal: ['Very Low', 'Low', 'Medium', 'High', 'Very High'],
        loading: false,
        formMode: 'create',

        register: [],
        regLoading: false,
        regid: '',
        rbslist: [],
        ownerlist: [],
        stepOne: true,
        steptwo: true,
        ltcolor: '',
        itcolor: '',
        search: '',
        item_id: '',
        headers: [{
                text: 'S.no',
                align: 'start',
                sortable: false,
                value: 'sno',
                class: 'title'
            },
            {
                text: 'Title',
                value: 'name',
                class: 'title'
            },
            {
                text: 'Type',
                value: 'type',
                class: 'title'
            },
            {
                text: 'RBS',
                value: 'rbs',
                class: 'title'
            },
            {
                text: 'Status',
                value: 'status',
                class: 'title'
            },
            {
                text: 'Approval',
                align: 'center',
                sortable: false,
                value: 'is_approved',
                class: 'title'
            },

            {
                text: 'Action',
                sortable: false,
                value: 'action',
                class: 'title'
            }

        ],
        riskItems: [],
        approvers: [],
        approver: '',
        approverSubmit: false,
        childKey: 1,
    }),
    validations: {
        des: {
            required,
            maxLength: maxLength(30)
        },
        rbs: {
            required
        },
        owner: {
            required
        },
        type: {
            required
        },
        cause: {
            required,
            maxLength: maxLength(300)
        },
        effect: {
            required,
            maxLength: maxLength(300)
        },
        detail: {
            required,
            maxLength: maxLength(300)
        },
        contigency: {
            required,
            numeric

        },

    },
    watch: {
        likelihood(val) {

            if (val != '') {
                if (val > 3) {
                    this.ltcolor = 'red'
                } else {
                    this.ltcolor = '';
                }
                this.score = val * this.impact
            }

        },
        impact(val) {

            if (val != '') {
                if (val > 3) {
                    this.itcolor = 'red'
                } else {
                    this.itcolor = '';
                }
                this.score = this.likelihood * val
            }

        }
    },
    computed: {

        causeErrors() {
            const errors = [];
            if (!this.$v.cause.$dirty) return errors;
            !this.$v.cause.maxLength && errors.push('Cause must be at most 300 characters long');
            !this.$v.cause.required && errors.push('Cause is required.');
            this.steptwovalidation();
            return errors
        },
        detailErrors() {
            const errors = [];
            if (!this.$v.detail.$dirty) return errors;
            !this.$v.detail.maxLength && errors.push('Detail must be at most 300 characters long');
            !this.$v.detail.required && errors.push('Detail is required.');
            this.steptwovalidation();
            return errors
        },
        effectErrors() {
            const errors = [];
            if (!this.$v.effect.$dirty) return errors;
            !this.$v.effect.maxLength && errors.push('Effect must be at most 300 characters long');
            !this.$v.effect.required && errors.push('Effect is required.');
            this.steptwovalidation();
            return errors
        },
        contigencyErrors() {
            const errors = [];
            if (!this.$v.contigency.$dirty) return errors;
            !this.$v.contigency.numeric && errors.push('Contigency should be numeric.');
            !this.$v.contigency.required && errors.push('Contigency is required.');
            this.steptwovalidation();
            return errors
        },
        desErrors() {
            const errors = [];
            if (!this.$v.des.$dirty) return errors;
            !this.$v.des.maxLength && errors.push('Name must be at most 30 characters long');
            !this.$v.des.required && errors.push('Name is required.');
            this.stepOnevalidation();
            return errors
        },
        rbsErrors() {
            const errors = [];
            if (!this.$v.rbs.$dirty) return errors;
            !this.$v.rbs.required && errors.push('RBS is required');
            this.stepOnevalidation();
            return errors;
        },
        ownerErrors() {
            const errors = [];
            if (!this.$v.owner.$dirty) return errors;
            !this.$v.owner.required && errors.push('Risk Owner is required');
            this.stepOnevalidation();
            return errors;
        },
        typeErrors() {
            const errors = [];
            if (!this.$v.owner.$dirty) return errors;
            !this.$v.owner.required && errors.push('Risk Type is required');
            this.stepOnevalidation();
            return errors;
        },
        checkApprover() {
            this.approverSubmit = false;
            if (this.approvers.some(app => app.id == this.approver)) {
                this.approverSubmit = true;
            }
        }

    },
    methods: {
        numWords() {
            //this.$v.contigency.$touch()
            var data = digits.numWords(this.contigency);
            return data;
        },
        stepOnevalidation() {
            if (this.$v.owner.$invalid || this.$v.type.$invalid || this.$v.rbs.$invalid || this.$v.des.$invalid) {
                this.stepOne = true;
            } else {
                this.stepOne = false;
            }
        },
        steptwovalidation() {
            if (this.$v.detail.$invalid || this.$v.cause.$invalid || this.$v.effect.$invalid || this.$v.contigency.$invalid) {
                this.steptwo = true;
            } else {
                this.steptwo = false;
            }
        },
        setDefault() {
            this.rbslist = this.$store.state.RbsData;
            this.ownerlist = this.$store.state.RiskOwnerData;
            this.formMode = 'create';
            this.stepOne = true;
            this.steptwo = true;
            this.reloadForm();
            this.formid = this.$route.params.id;
            this.$v.$reset();
            this.e6 = 1;
            this.formdialog = true;

        },

        editItem(item) {
            this.e6 = 1;
            this.rbslist = this.$store.state.RbsData;
            this.ownerlist = this.$store.state.RiskOwnerData;
            this.stepOne = false;
            this.steptwo = false;
            this.$v.$reset();

            this.item_id = item.item_id;

            this.type = item.type_id.toString();

            this.rbs = item.rbs_id;
            this.owner = item.owner_id;
            this.des = item.des;
            this.cause = item.cause;
            this.effect = item.effect;
            this.detail = item.detail;
            this.contigency = item.contigency;
            this.likelihood = item.likelihood;
            this.impact = item.impact;
            this.score = item.score;
            this.formMode = 'update';
            this.formdialog = true;
        },
        reloadForm() {
            this.type = '';
            this.rbs = '';
            this.owner = '';

            this.des = '';
            this.cause = '';
            this.effect = '';
            this.detail = '';
            this.contigency = '';

            this.likelihood = 1;
            this.impact = 1;
            this.score = 1;
            this.formMode = 'create';
            this.$v.$reset();
        },
        subForm(rid = 0) {
            if (this.formLoader) {
                return;
            }
            this.$v.$reset();
            this.formLoader = true;
            const formData = {};
            formData._token = document.getElementById('csrf').content;
            formData.type = this.type;
            formData.rbs = this.rbs;
            formData.owner = this.owner;

            formData.des = this.des;
            formData.cause = this.cause;
            formData.effect = this.effect;
            formData.detail = this.detail;
            formData.contigency = this.contigency;

            formData.likelihood = this.likelihood;
            formData.impact = this.impact;
            formData.score = this.score;

            let url = '';

            if (this.formMode == 'create') {
                formData.id = this.formid;
                url = window.links.storeItem;
            } else {
                formData.id = this.item_id;
                url = window.links.updateItem;
            }

            this.$http.post(url, formData)
                .then(res => {
                    if (res.status == 200) {
                        this.$store.commit('snackNotify', {
                            type: 'success',
                            msg: res.data.message
                        })
                        this.formdialog = false;
                        this.reloadForm();
                    }

                })
                .catch(err => {
                    let errText = '';
                    if (err.response.status == 422) {
                        Object.values(err.response.data.errors).forEach(val => {
                            errText += val + '<br>';
                        });
                        this.$store.commit('snackNotify', {
                            type: 'error',
                            msg: res.data.errText
                        })
                    } else {
                        console.log(err.response);
                        this.$store.commit('snackNotify', {
                            type: 'error',
                            msg: 'Something went wrong'
                        })
                        this.formdialog = false

                    }

                }).then(() => {
                    this.formLoader = false;
                    this.getRiskItems();
                })

        },
        updateForm() {
            this.subForm(this.item_id)
        },

        getRiskItems() {
            this.dashLoaded=false;
            const formData = {};
            formData._token = document.getElementById('csrf').content;
            formData.id = this.$route.params.id;

            this.$http.post(window.links.getRiskItems, formData).then((response) => {
                const dt = response.data.data;
                const reg = response.data.reg;
                this.riskItems = dt;
                this.register = reg[0];
                this.childKey++;

            }).catch(
                (error) => {
                    this.$store.commit('snackNotify', {
                        type: 'error',
                        msg: 'Item Fetching Error'
                    })
                }
            ).then(() => {
                if (this.riskItems.length == 0) {
                    this.noItem = true;
                } else {
                    this.noItem = false;
                }
                this.$store.state.isLoading = false;

                this.$http.post(window.links.getRiskItemDashboard, formData).then((response) => {
                    const dt = response.data;
                    this.register.dashboard = dt;
                    this.dashLoaded=true;
                    this.childKey++;
                    
                })
            })

        },
        viewItem(item) {

            this.$router.push({
                name: 'DetailItem',
                params: {
                    id: item.item_id
                }
            })
        },
        deactivateRegister() {
            if (this.$store.state.isLoading) {
                return;
            }
            this.$store.state.isLoading = true;
            const formData = {};
            formData._token = document.getElementById('csrf').content;
            formData.id = this.$route.params.id;

            this.$http.post(window.links.deactivateRegister, formData).then((response) => {
                this.$store.commit('snackNotify', {
                    type: 'success',
                    msg: response.data.message
                })

            }).catch(
                (error) => {
                    this.$store.commit('snackNotify', {
                        type: 'error',
                        msg: 'Error Updating'
                    })
                }
            ).then(() => {
                this.delDialog = false;
                if (this.register.status == 2) {
                    this.register.status = 1;
                } else {
                    this.register.status = 2;
                }
                this.$store.state.isLoading = false;
            })
        },
        delItem(item) {
            if (this.$store.state.isLoading) {
                return;
            }
            this.$store.state.isLoading = true;
            const formData = {};
            formData._token = document.getElementById('csrf').content;
            formData.id = item.item_id;

            this.$http.post(window.links.deleteItem, formData).then((response) => {
                this.$store.commit('snackNotify', {
                    type: 'success',
                    msg: response.data.message
                })

            }).catch(
                (error) => {
                    this.$store.commit('snackNotify', {
                        type: 'error',
                        msg: 'Error Updating'
                    })
                }
            ).then(() => {
                this.$store.state.isLoading = false;
                this.getRiskItems();
            })
        }

    },

    created() {
        this.$store.state.isLoading = true;
        this.getRiskItems();

        if (!(Array.isArray(this.$store.state.RbsData) && this.$store.state.RbsData.length)) {
            this.$store.dispatch('getRbs');
        }

        if (!(Array.isArray(this.$store.state.RiskOwnerData) && this.$store.state.RiskOwnerData.length)) {
            this.$store.dispatch('getOwner');
        }

        this.approvers = Approver;

    },
    components: {
        'item-dashboard': ItemsDashboard
    }

}
</script>
