<template>
<div>
    <div class="mt-n3">
        <div class="font-weight-bold display-1">Risk Register

            <div class="float-right">
                <v-row justify="center">
                    <v-dialog v-model="formdialog" v-if="$can('Create RiskRegister')" persistent max-width="600px">
                        <template v-slot:activator="{ on }">
                            <v-btn class="mx-2" v-if="$can('Create RiskRegister')" fab dark v-on="on" @click="changeMode()" color="indigo">
                                <v-icon dark>fas fa-plus</v-icon>
                            </v-btn>

                        </template>
                        <v-card>
                            <v-progress-linear :active="loading" :indeterminate="true" absolute top color="deep-purple accent-4"></v-progress-linear>

                            <v-card-title>
                                <span class="headline">Add new Register</span>
                            </v-card-title>
                            <v-card-text>
                                <v-container>
                                    <v-row>
                                        <v-col cols="12" md="12">
                                            <v-text-field v-model="name" :error-messages="nameErrors" :counter="30" label="Register Tilte*" required @input="$v.name.$touch()" @blur="$v.name.$touch()"></v-text-field>
                                        </v-col>
                                        <v-col cols="12" md="12">
                                            <v-text-field v-model="person" :error-messages="personErrors" label="Responsible Person *" required @input="$v.person.$touch()" @blur="$v.person.$touch()"></v-text-field>
                                        </v-col>
                                        <v-col cols="12" md="12">
                                            <v-autocomplete :items="projectList" item-text="name" item-value="unique" v-model="project" :error-messages="projectErrors" @input="$v.project.$touch()" @blur="$v.project.$touch()" label="Project*"></v-autocomplete>
                                            <!-- <v-text-field v-model="project" :error-messages="projectErrors" label="Project / Bid *" required @input="$v.project.$touch()" @blur="$v.project.$touch()"></v-text-field> -->
                                        </v-col>
                                        <v-col cols="12" md="12">
                                            <v-autocomplete :items="buList" item-text="name" item-value="id" :error-messages="buErrors" v-model="bu" @input="$v.bu.$touch()" @blur="$v.bu.$touch()" label="Business Unit*"></v-autocomplete>
                                        </v-col>
                                        <v-card-text>
                                            <v-autocomplete :items="approvers" item-text="name" item-value="id" v-model="approver" :error-messages="approverErrors" @input="$v.approver.$touch()" @blur="$v.approver.$touch()" label="Select Approver*"></v-autocomplete>
                                        </v-card-text>

                                    </v-row>
                                </v-container>
                                <small>*indicates required field</small>
                            </v-card-text>
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="blue darken-1" text @click="formdialog = false">Close</v-btn>

                                <v-btn color="blue darken-1" v-if="formMode=='create'" :disabled="$v.$invalid || loading" text @click="subForm()">Save</v-btn>

                                <v-btn color="blue darken-1" v-if="formMode=='update'" :disabled="$v.$invalid || loading" text @click="updateForm()">Update</v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-dialog>
                </v-row>
            </div>
        </div>
        <v-divider class="mt-5"></v-divider>
    </div>
    <div class="mx-auto mt-4" :class="'mw7'" v-if="regLoading">
        <v-card v-for="x in 5" :key="x">
            <v-skeleton-loader class="mx-auto mt-4" max-width="700" ref="skeleton" :type="'list-item-three-line'"></v-skeleton-loader>
        </v-card>
    </div>
    <div v-if="!regLoading">
        <div class="mx-auto mt-4" style=" max-width: 700px;">
            <v-text-field v-model="search" name="search" @input="filter()" label="Search Register" id="search"></v-text-field>
        </div>
        <transition-group name="fadeUp" enter-active-class="fadeInUp" leave-active-class="">
            <v-hover v-for="(r,index) in f_registers.slice(0, 6)" :key="index+r.name" v-slot:default="{ hover }" open-delay="200">

                <v-card :elevation="hover ? 12 : 2" class="mx-auto mt-4" max-width="700" shaped>

                    <v-card-title primary-title>

                        {{r.name}}
                        <v-spacer></v-spacer>
                        <v-chip v-if="r.status==2" class="ma-2" color="teal" text-color="white">
                            <v-avatar left>
                                <v-icon>fas fa-unlink</v-icon>
                            </v-avatar>
                            Unlinked
                        </v-chip>

                        <v-btn class="mx-2" fab small dark color="indigo" @click.prevent="openItem(r)">
                            <v-icon dark>fas fa-search</v-icon>
                        </v-btn>
                        <v-btn class="mx-2" v-if="$can('Edit RiskRegister')" small @click.prevent="editRegister(r)" fab dark color="cyan">
                            <v-icon dark>fas fa-pencil-alt</v-icon>
                        </v-btn>
                    </v-card-title>

                    <v-card-subtitle>{{r.bu}}</v-card-subtitle>
                    <v-card-text>
                        Created on- {{r.date}}
                    </v-card-text>

                </v-card>

            </v-hover>
        </transition-group>

        <v-alert v-if="noReg" type="error">
            No registers Found
        </v-alert>

    </div>

</div>
</template>

<style scoped>
.mw7 {
    max-width: 700px;
    ;
}
</style>

<script>
import {
    required,
    maxLength
} from 'vuelidate/lib/validators'

export default {
    data: () => ({
        noReg: false,
        formdialog: false,
        buList: [],
        projectList: [],
        name: '',
        person: '',
        project: '',
        bu: '',
        loading: false,
        formMode: 'create',
        registers: [],
        regLoading: true,
        regid: '',
        approvers: [],
        approver: '',
        f_registers: [],
        search: '',

    }),
    validations: {
        name: {
            required,
            maxLength: maxLength(30)
        },
        person: {
            required
        },
        project: {
            required
        },
        bu: {
            required
        },
        approver: {
            required
        }
    },
    computed: {

        approverErrors() {
            const errors = [];
            if (!this.$v.approver.$dirty) return errors;
            !this.$v.approver.required && errors.push('Approver Unit is required');
            return errors;
        },
        nameErrors() {
            const errors = [];
            if (!this.$v.name.$dirty) return errors;
            !this.$v.name.maxLength && errors.push('Name must be at most 30 characters long');
            !this.$v.name.required && errors.push('Name is required.');
            return errors
        },
        buErrors() {
            const errors = [];
            if (!this.$v.bu.$dirty) return errors;
            !this.$v.bu.required && errors.push('Business Unit is required');
            return errors;
        },
        personErrors() {
            const errors = [];
            if (!this.$v.person.$dirty) return errors;
            !this.$v.person.required && errors.push('Responsible Person is required');
            return errors;
        },
        projectErrors() {
            const errors = [];
            if (!this.$v.project.$dirty) return errors;
            !this.$v.project.required && errors.push('Project/Bid Unit is required');
            return errors;
        },

    },
    methods: {
        filter() {
            if (this.search != '') {
                var arr = this.registers;
                this.f_registers = [];
                for (var key in arr) {
                    var obj = arr[key];
                    console.log(obj);
                    if (obj.name.toLowerCase().indexOf(this.search.toLowerCase()) >= 0) {

                        this.f_registers.push(obj);
                    }
                }

            } else {
                this.f_registers = this.registers;
            }

        },
        openItem(reg) {
            this.$store.state.itemData = reg;
            this.$router.push({
                name: 'RegisterItem',
                params: {
                    id: reg.rid
                }
            })

        },
        changeMode() {
            this.formMode = 'create';
            this.reloadForm();
        },
        getRegister() {
            this.regLoading = true;

            this.$http.get(window.links.getRegister).then((response) => {
                const dt = response.data.data;
                this.registers = dt;
                this.f_registers = this.registers;
                this.buList = response.data.bu;
                this.projectList = response.data.project;
                if (this.registers.length == 0) {
                    this.noReg = true;
                } else {
                    this.noReg = false;
                }

            }).catch(
                (error) => {
                    console.log("Register Ajax error logged");
                    this.$store.commit('errorFunction', error.response);
                }
            ).then(() => {

                this.regLoading = false;
                this.$store.state.isLoading = false;

            })

        },
        editRegister(r) {
            this.name = r.name;
            this.person = r.person;
            this.bu = r.buid;
            this.project = r.project;
            this.regid = r.rid;
            this.approver = r.approver;
            this.formMode = 'update';
            this.formdialog = true;
        },
        reloadForm() {
            this.name = '';
            this.person = '';
            this.bu = '';
            this.project = '';
            this.formMode = 'create';
             this.$v.$reset();
        },
        subForm(rid = 0) {
            if (this.loading) {
                return;
            }
            this.$v.$reset();
            this.loading = true;
            const formData = {};
            formData._token = document.getElementById('csrf').content;
            formData.name = this.name;
            formData.person = this.person;
            formData.bu = this.bu;
            formData.approver = this.approver;
            formData.project = this.project;

            let url = '';

            if (this.formMode == 'create') {
                url = window.links.createRegister;
            } else {
                url = window.links.updateRegister;
                formData.id = rid;
            }

            this.$http.post(url, formData)
                .then(res => {
                    if (res.status == 200) {
                        this.$store.commit('snackNotify', {
                            type: 'success',
                            msg: res.data.message
                        })
                        this.formdialog = false
                        this.$v.$reset();
                    }

                    this.getRegister();

                })
                .catch(err => {
                    this.$store.commit('errorFunction', error);
                }).then(() => {
                    this.loading = false;
                })

        },
        updateForm() {
            this.subForm(this.regid)
        }

    },
    created() {
        this.$store.state.isLoading = true;
        this.getRegister()
        this.approvers = Approver;

    }
}
</script>
