<template>
<div>
    <v-card>
        <v-row :justify="'start'" class="ma-sm-4 pa-sm-4">
            <v-btn color="primary" x-small @click="back()" fab dark>
                <v-icon>fas fa-arrow-circle-left</v-icon>
            </v-btn>
            <v-spacer></v-spacer>
            <v-btn color="success" v-if="!riskItem.is_approved==1 && riskItem.approver" @click="Approvedialog=true">Approve</v-btn>
            <v-btn color="success" v-if="riskItem.is_approved==1  && riskItem.final_contigency!=null && !riskItem.closure_requested &&riskItem.is_closed==0" @click="requestClosure()">Request Closure</v-btn>
            <v-btn color="success" v-if="riskItem.is_approved==1 && riskItem.approver &&riskItem.final_contigency!=null && riskItem.closure_requested &&riskItem.is_closed==0" @click="Approvedialog=true">Approve Closure</v-btn>
        </v-row>

        <v-card-title class="text-center justify-center py-2 font-weight-bold  display-1">

            {{riskItem.name}}

        </v-card-title>

        <v-tabs v-model="tab" background-color="transparent" color="basil" grow>
            <v-tab>
                Basic Info
            </v-tab>
            <v-tab>
                Scores
            </v-tab>
            <v-tab>
                Contingency
            </v-tab>
            <v-tab>

                Actions
                <span v-if="Number(riskItem.pendingActions)>=1">
                    <v-badge color="red" inline :content="riskItem.pendingActions">

                    </v-badge>
                </span>

            </v-tab>
            <v-tab>
                Files
            </v-tab>
        </v-tabs>

        <v-tabs-items v-model="tab">
            <!-- Basic tab -->
            <v-tab-item>
                <basic-detail v-if="riskItem" :riskItem="riskItem" :item_id="item_id" @refresh="refresh()"></basic-detail>
            </v-tab-item>
            <!-- End of Basic Tab -->
            <v-tab-item>
                <scores v-if="riskItem" :riskItem="riskItem" :item_id="item_id" @refresh="refresh()"></scores>
            </v-tab-item>
            <v-tab-item>
                <contigency v-if="riskItem" :riskItem="riskItem" :item_id="item_id" @refresh="refresh()"></contigency>
            </v-tab-item>
            <!-- End of Score Tab -->
            <v-tab-item>
                <v-card color="basil" flat :elevation="3">

                    <div style='max-width:760px;' class="mx-auto  mt-5">

                        <action :item_id="item_id" v-if="riskItem" :riskItem="riskItem" @reset="refresh()" @filter="filter"></action>
                    </div>

                    <v-row :justify="'center'">
                        <v-col cols="12" :md="8" :xs="12">
                            <v-card v-if="actions.length==0" :elevation="3" class="p-md-3">
                                <v-card-title class="title">
                                    <v-icon color="primary" class="mr-1">fas fa-atom</v-icon>
                                    <span class="headline" color="red">No Actions Found</span>

                                </v-card-title>
                            </v-card>
                        </v-col>
                    </v-row>

                    <v-row :justify="'center'">
                        <v-col cols="12" :md="8" :xs="12" v-for="(a,k) in actions" :key="k">
                            <v-card :elevation="3" class="p-md-3">
                                <v-card-title class="title">
                                    <v-icon v-if="!a.completed" color="primary" class="mr-1">fas fa-atom</v-icon>
                                    <v-icon v-if="a.completed" color="success" class="mr-1">fas fa-check</v-icon> Action

                                    <v-spacer></v-spacer>
                                    <span v-if="$can('Complete RiskAction')">
                                        <v-btn class="mx-2" v-if="!a.completed" @click="completeAction(a.action_id)" dark fab small color="success">
                                            <v-icon>fas fa-check</v-icon>
                                        </v-btn>
                                    </span>

                                    <span v-if="$can('Delete RiskAction')">
                                        <v-btn class="mx-2" @click="deleteAction(a.action_id)" dark fab small color="error">
                                            <v-icon>fas fa-trash</v-icon>
                                        </v-btn>
                                    </span>

                                </v-card-title>
                                <v-card-subtitle class=" pl-md-10">
                                    {{a.date}}
                                </v-card-subtitle>

                                <v-card-text class=" pl-md-10">

                                    {{a.action}}
                                </v-card-text>
                                <v-divider class="m-0"></v-divider>
                                <v-card-text class=" p-md-0 pl-md-10">
                                    <v-row :justify="'space-around'" :align="'end'">
                                        <v-col cols="12" :md="4" :xs="6">
                                            <v-icon>fas fa-user-tie</v-icon>
                                            <span class="subtitle-1 ml-2">{{a.person}}</span>
                                        </v-col>
                                        <v-col cols="12" :md="4" :xs="6">
                                            <v-icon>fas fa-stopwatch</v-icon>
                                            <span class="subtitle-1 ml-2">{{a.target}}</span>
                                        </v-col>
                                        <v-col cols="12" :md="4" :xs="6">
                                            <span v-if="a.completed">
                                                <v-icon>fas fa-calendar-check</v-icon>
                                                <span class="subtitle-1 ml-2">{{a.completion_date}}</span>
                                            </span>
                                        </v-col>

                                    </v-row>
                                    <!-- <div style="align-items: baseline;display: flex;">
                                        <v-icon>fas fa-user-tie</v-icon>
                                        <span class="subtitle-1 ml-2">{{a.person}}</span>
                                        <v-spacer></v-spacer>
                                        <v-icon>fas fa-stopwatch</v-icon>
                                        <span class="subtitle-1 ml-2">{{a.target}}</span>
                                        <v-spacer></v-spacer>
                                        <span v-if="a.completed">
                                            <v-icon>fas fa-calendar-check</v-icon>
                                            <span class="subtitle-1 ml-2">{{a.completion_date}}</span>
                                        </span>

                                    </div> -->

                                </v-card-text>
                                <div></div>

                            </v-card>
                        </v-col>
                    </v-row>

                </v-card>
            </v-tab-item>
            <v-tab-item>
                <v-card color="basil" flat>
                    <v-card-text>
                        <file-upload :files="files" :item_id="item_id" @refresh="refresh()"></file-upload>
                    </v-card-text>
                </v-card>
            </v-tab-item>
        </v-tabs-items>
    </v-card>

    <!-- Approve Dialog -->
    <v-dialog v-model="Approvedialog" max-width="400">
        <v-card>
            <v-card-title class="headline">Approve Register</v-card-title>

            <v-card-text>
                <v-textarea clearable clear-icon="fas fa-times" label="Reason for Decision" :error-messages="reasonErrors" :counter="300" v-model="reasonApprove" outlined></v-textarea>
            </v-card-text>

            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="green darken-1" text :disabled="(!riskItem.approver || reasonErrors!='')" @click=" approvalSubmission(1)">
                    Approve
                </v-btn>
                <v-btn color="red darken-1" text :disabled="(!riskItem.approver || reasonErrors!='')" @click="approvalSubmission(2)">
                    Reject
                </v-btn>

            </v-card-actions>
        </v-card>
    </v-dialog>

</div>
</template>

<style>
.bgtbl {
    background-color: #b8daff !important;
    color: rgb(0, 0, 0) !important;
    font-size: 16px;
}

.txtb {
    color: rgb(0, 0, 0) !important;
    font-size: 18px;
}
</style>

<script>
import axios from 'axios';
import Action from './Action';
import BasicDetail from './details/BasicDetail';
import Scores from './details/Scores';
import FileUpload from './details/FileUpload';
import Contigency from './details/Contigency';

export default {
    components: {
        Action,
        BasicDetail,
        'scores': Scores,
        FileUpload,
        Contigency,

    },
    data: () => ({

        tab: null,
        fab: false,
        item_id: '',
        riskItem: '',
        actions: [],
        files: [],
        reasonApprove: '',
        Approvedialog: false,

    }),
    methods: {
        back() {
            this.$router.push({
                name: 'RegisterItem',
                params: {
                    'id': this.riskItem.reg_id
                }
            })
        },
        refresh() {
            this.getItem(this.item_id);
        },
        filter(val) {

            if (val != 1) {
                var arr = this.riskItem.actions;
                this.actions = [];
                for (var key in arr) {
                    var obj = arr[key];

                    if (val == 2) {
                        if (obj.completed) {
                            this.actions.push(obj);
                        }
                    }
                    if (val == 3) {
                        if (!obj.completed) {
                            this.actions.push(obj);
                        }
                    }
                }

            } else {
                this.actions = this.riskItem.actions;
            }

        },
        approveRegister(id, mode = 'initial') {
            if (this.$store.state.isLoading) {
                return;
            }
            this.$store.state.isLoading = true;

            const formData = {};
            formData._token = document.getElementById('csrf').content;
            formData.item_id = this.item_id;
            formData.reason = this.reasonApprove;
            formData.decision = id;
            var link = '';
            if (mode == 'initial') {
                link = window.links.approveItem;
            } else {
                link = window.links.closeItem;
            }

            this.$http.post(link, formData)
                .then(res => {
                    if (res.status == 200) {
                        this.$store.state.snackText = res.data.message;
                        this.$store.state.snackColor = 'success';
                        this.$store.state.snackbar = true;
                        this.Approvedialog = false;
                    }

                })
                .catch(err => {
                    this.$store.commit('errorFunction', err);

                }).then(() => {
                    this.$store.state.isLoading = false;
                    this.getItem(this.item_id);
                })
        },
        requestClosure(id) {
            if (this.$store.state.isLoading) {
                return;
            }
            if (this.riskItem.pendingActions == 0) {
                this.$store.state.isLoading = true;
                const formData = {};
                formData._token = document.getElementById('csrf').content;
                formData.id = this.item_id;
                formData.decision = id;
                formData.reason = this.reasonApprove;

                this.$http.post(window.links.requestClosure, formData)
                    .then(res => {
                        if (res.status == 200) {
                            this.$store.commit('snackNotify', {
                                type: 'success',
                                msg: res.data.message
                            })

                            this.Approvedialog = false;
                        }

                    })
                    .catch(err => {
                        this.$store.commit('errorFunction', err);

                    }).then(() => {
                        this.$store.state.isLoading = false;
                        this.getItem(this.item_id);
                    })
            } else {

                this.$store.commit('snackNotify', {
                    type: 'error',
                    msg: 'Actions are Pending'
                })

            }
        },
        approvalSubmission(id) {
            if (this.riskItem.is_approved == 1) {
                this.approveRegister(id, 'final');
            } else {
                this.approveRegister(id);
            }
        },

        getItem(id) {
            this.$store.state.isLoading = true;
            const formData = {};
            formData._token = document.getElementById('csrf').content;
            formData.id = id;
            this.$http.post(window.links.getItem, formData).then((response) => {
                const dt = response.data;
                Object.values(dt)[0].forEach((val) => {

                    this.riskItem = val;
                })
                this.actions = this.riskItem.actions;
                this.files = this.riskItem.files;

            }).catch(
                (error) => {
                    console.log("Items Ajax error logged");
                }
            ).then(() => {
                this.$store.state.isLoading = false;
            })

        },

        refresh() {
            this.getItem(this.$route.params.id);
        },
        deleteAction(id) {
            if (this.$store.state.isLoading) {
                return;
            }
            const formData = {};
            formData.id = id;
            formData._token = document.getElementById('csrf').content;
            this.$store.state.isLoading = true;
            axios.post(window.links.deleteAction, formData).then(res => {
                    if (res.status == 200) {
                        this.$store.commit('snackNotify', {
                            type: 'success',
                            msg: res.data.message
                        })
                        this.formdialog = false;

                    }

                })
                .catch(err => {
                    let errText = '';
                    if (err.response.status == 422) {
                        Object.values(err.response.data.errors).forEach(val => {
                            errText += val + '<br>';
                        });
                        this.$store.commit('snackNotify', {
                            type: 'error',
                            errText
                        })
                    } else {
                        console.log(err.response);
                        this.$store.commit('snackNotify', {
                            type: 'success',
                            msg: 'Something went wrong'
                        })
                        this.formdialog = false;

                    }

                }).then(() => {
                    this.getItem(this.item_id);
                    this.$store.state.isLoading = false;
                })
        },
        completeAction(id) {
            if (this.$store.state.isLoading) {
                return;
            }
            const formData = {};
            formData.id = id;
            formData._token = document.getElementById('csrf').content;
            this.$store.state.isLoading = true;

            axios.post(window.links.completeAction, formData).then(res => {
                    if (res.status == 200) {
                        this.snackText = res.data.message;
                        this.snackColor = 'success';
                        this.snackbar = true;
                        this.formdialog = false;

                    }
                    this.getItem(this.item_id);

                })
                .catch(err => {
                    let errText = '';
                    if (err.response.status == 422) {
                        Object.values(err.response.data.errors).forEach(val => {
                            errText += val + '<br>';
                        });
                        this.snackText = errText;
                        this.snackColor = 'error';
                        this.snackbar = true;
                    } else {
                        console.log(err.response);
                        this.snackText = 'Something went wrong';
                        this.snackColor = 'error';
                        this.snackbar = true;
                        this.formdialog = false;
                        this.getItem(this.item_id);
                    }

                }).then(() => {
                    this.$store.state.isLoading = false;
                })
        }
    },
    computed: {
        reasonErrors() {
            if (this.reasonApprove == '') {
                return 'Mention reason for this decision'
            }
            return '';
        },
    },
    created() {
        //this.$store.state.isLoading = true;
        this.item_id = this.$route.params.id;

        this.getItem(this.item_id);

    }
}
</script>
