<template>
<div>
    <v-card color="basil" flat :elevation="3">
        <v-card-text>
            <v-alert type="error" v-if="riskItem.is_reject">
                {{riskItem.reject_reason}}
            </v-alert>
            <v-row>
                <v-col cols="12" :xs="6" :md="4">
                    <v-card>
                        <v-card-title primary-title>
                            Type
                        </v-card-title>
                        <v-divider class="m-0" inset></v-divider>
                        <v-card-text class="txtb">
                            {{riskItem.type}}
                        </v-card-text>
                    </v-card>
                </v-col>
                <v-col cols="12" :xs="6" :md="4">

                    <v-card>
                        <v-card-title primary-title>
                            RBS
                        </v-card-title>
                        <v-divider class="m-0" inset></v-divider>
                        <v-card-text class="txtb">
                            {{riskItem.rbs}}
                        </v-card-text>
                    </v-card>
                </v-col>
                <v-col cols="12" :xs="6" :md="4">
                    <v-card>
                        <v-card-title primary-title>
                            Owner
                        </v-card-title>
                        <v-divider class="m-0" inset></v-divider>
                        <v-card-text class="txtb">
                            {{riskItem.owner}}
                        </v-card-text>
                    </v-card>
                </v-col>
                <v-col cols="12" :xs="6" :md="4">
                    <v-card>
                        <v-card-title primary-title>
                            Created on
                        </v-card-title>
                        <v-divider class="m-0" inset></v-divider>
                        <v-card-text class="txtb">
                            {{riskItem.created}}
                        </v-card-text>
                    </v-card>
                </v-col>
                <v-col cols="12" :xs="6" :md="4">
                    <v-card>
                        <v-card-title primary-title>
                            Status
                            <v-spacer></v-spacer>
                            <v-btn icon v-if="$can('Update RiskStatus') && riskItem.editPerm" @click='statusDialog = true'>
                                <v-icon color="warning">fas fa-pencil-alt</v-icon>
                            </v-btn>
                        </v-card-title>
                        <v-divider class="m-0" inset></v-divider>
                        <v-card-text class="txtb">
                            {{riskItem.status}}

                        </v-card-text>
                    </v-card>

                </v-col>

            </v-row>

            <v-row :justify="'center'">
                <v-col :xs="12" :md="8">
                    <v-card>
                        <v-card-title primary-title>
                            Detail Description
                        </v-card-title>
                        <v-divider class="m-0" inset></v-divider>
                        <v-card-text class="txtb">
                            {{riskItem.detail}}
                        </v-card-text>
                    </v-card>
                </v-col>
            </v-row>
            <v-row :justify="'center'">
                <v-col cols="12" :sm="12" :md="8">
                    <v-card>
                        <v-card-title primary-title>
                            Cause
                        </v-card-title>
                        <v-divider class="m-0" inset></v-divider>
                        <v-card-text class="txtb">
                            {{riskItem.cause}}
                        </v-card-text>
                    </v-card>
                </v-col>
                <v-col cols="12" :sm="12" :md="8">
                    <v-card>
                        <v-card-title primary-title>
                            Effect
                        </v-card-title>
                        <v-divider class="m-0" inset></v-divider>
                        <v-card-text class="txtb">
                            {{riskItem.effect}}
                        </v-card-text>
                    </v-card>
                </v-col>
            </v-row>
        </v-card-text>
    </v-card>

    <v-dialog v-model="statusDialog" max-width="400">
        <v-card>
            <v-card-title class="headline">Update the Status</v-card-title>

            <v-card-text>
                <v-row>
                    <v-col class="d-flex" cols="12" sm="12">
                        <v-select :items="riskItem.allstatus" item-value="id" item-text="name" label="Status" v-model="changedStatus" solo></v-select>
                    </v-col>

                </v-row>
            </v-card-text>

            <v-card-actions>
                <v-spacer></v-spacer>

                <v-btn color="danger" text @click="statusDialog = false">
                    close
                </v-btn>

                <v-btn color="green darken-1" text @click="updateStatus()">
                    Update
                </v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</div>
</template>

<script>
export default {
    props: {
        riskItem: {
            type: Object,
            required: true,

        },
        item_id: {
            type: String
        }
    },
    data: () => ({
        changedStatus: '',
        statusDialog: false,

    }),
    methods: {
        updateStatus() {
            if (this.changedStatus == this.riskItem.status_id) {
                this.$store.commit('snackNotify', {
                    type: 'error',
                    msg: 'No changes detected'
                })
                this.statusDialog = false;

            } else {
                const formData = {};
                formData.id = this.item_id;
                formData.status = this.changedStatus;
                formData._token = document.getElementById('csrf').content;

                this.$http.post(window.links.changeStatus, formData).then(res => {
                        this.$store.commit('snackNotify', {
                            type: 'success',
                            msg: 'Status Changed'
                        })
                        this.$emit('refresh');
                        this.statusDialog = false;
                    })
                    .catch(err => {
                        this.$store.commit('errorFunction', err);

                    })

            }

        }
    }
}
</script>
