<template>
<div>
    <v-row class="m-0">
        <v-col class="text-left my-auto" :md="6"><span class="headline font-weight-bold"> Mitigation Actions</span></v-col>
        <v-col class="text-right" :md="6">
            <v-menu open-bottom offset-y>
                <template v-slot:activator="{ on }">
                    <v-btn style=" outline: none" color="primary" fab dark v-on="on">
                        <v-icon>fas fa-filter</v-icon>
                    </v-btn>
                </template>

                <v-list>
                    <v-list-item-group color="primary">
                        <v-list-item @click="filter(1)">
                            <v-list-item-content >
                                <v-list-item-title >All</v-list-item-title>
                            </v-list-item-content>
                        </v-list-item>
                        <v-list-item @click="filter(2)">
                            <v-list-item-content >
                                <v-list-item-title >View Completed</v-list-item-title>
                            </v-list-item-content>
                        </v-list-item>
                        <v-list-item @click="filter(3)">
                            <v-list-item-content>
                                <v-list-item-title >View Pending</v-list-item-title>
                            </v-list-item-content>
                        </v-list-item>
                    </v-list-item-group>
                </v-list>
            </v-menu>

            <v-dialog v-model="Actiondialog" persistent max-width="600px">
                <template v-slot:activator="{ on }">
                    <v-btn style=" outline: none" v-if="$can('Create RiskAction') && riskItem.editPerm"  title="Add Action" class="mx-2 text-right" v-on="on" @click="setDefault()" fab dark color="indigo">
                        <v-icon dark>fas fa-plus</v-icon>
                    </v-btn>

                </template>
                <v-card>
                    <v-card-title>
                        <span class="headline">Create Actions</span>
                    </v-card-title>
                    <v-divider class="m-0"></v-divider>
                    <v-card-text>
                        <v-row>
                            <v-col cols="12" md="12">
                                <v-textarea clearable clear-icon="fas fa-times" label="Action Required" :error-messages="actionErrors" :counter="300" v-model="action" outlined @input="$v.action.$touch()" @blur="$v.action.$touch()"></v-textarea>
                            </v-col>

                            <v-col cols="12" md="12">
                                <v-text-field v-model="person" :error-messages="personErrors" :counter="30" label="Responsible Person*" required @input="$v.person.$touch()" @blur="$v.person.$touch()"></v-text-field>
                            </v-col>
                            <v-col cols="12" md="12">
                                <v-text-field v-model="email" :error-messages="emailErrors" label="Responsible Person Email*" required @input="$v.email.$touch()" @blur="$v.email.$touch()"></v-text-field>
                            </v-col>
                            <v-col cols="12" md="12">
                                <v-menu ref="datemenu" v-model="datemenu" :close-on-content-click="false" transition="scale-transition" offset-y max-width="290px" min-width="290px">
                                    <template v-slot:activator="{ on }">
                                        <v-text-field v-model="target" label="Target Date" :error-messages="targetErrors" persistent-hint prepend-icon="fas fa-calendar" v-on="on" @input="$v.target.$touch()" @blur="$v.target.$touch()"></v-text-field>
                                    </template>
                                    <v-date-picker v-model="target" :min="today" no-title @input="datemenu = false"></v-date-picker>
                                </v-menu>
                            </v-col>
                        </v-row>

                        <small>*indicates required field</small>
                    </v-card-text>
                    <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn color="blue darken-1" text @click="Actiondialog = false">Close</v-btn>
                        <v-btn color="blue darken-1" text @click="subForm()">Save</v-btn>
                    </v-card-actions>
                </v-card>
            </v-dialog>
        </v-col>
    </v-row>

</div>
</template>

<style>

</style>

<script>
import {
    required,
    maxLength,
    email
} from 'vuelidate/lib/validators'

import axios from 'axios';
export default {
    props: {
                riskItem: {
            type: Object,
            required: true
        },
        item_id: {
            type: String,
            required: true
        }
    },
    data: () => {
        return {
            Actiondialog: false,
            datemenu: false,
            dateFormatted: '',
            today: '',
            action: '',
            email: '',
            target: '',
            person: '',

        }
    },
    validations: {
        'email': {
            required,
            email
        },
        'action': {
            required,
            maxLength: maxLength(400)
        },
        'person': {
            required,
            maxLength: maxLength(30)
        },
        'target': {
            required
        }
    },
    methods: {
        setDefault() {
            this.$v.$reset();
            this.action = '';
            this.email = '';
            this.target = '';
            this.person = '';
            this.today = new Date().toISOString().substr(0, 10);

        },
        subForm() {
            const formData = {};
            formData.action = this.action;
            formData.email = this.email;
            formData.person = this.person;
            formData.target = this.target;
            formData.item_id = this.item_id;
            formData._token = document.getElementById('csrf').content;

            axios.post(window.links.createAction, formData).then(res => {
                    if (res.status == 200) {
                        this.$store.commit('snackNotify',{type:'success',msg:res.data.message})
                        this.Actiondialog = false;
                    }
                    this.$emit('reset');
                })
                .catch(err => {
                    let errText = '';
                    if (err.response.status == 422) {
                        Object.values(err.response.data.errors).forEach(val => {
                            errText += val + '<br>';
                        });
                         this.$store.commit('snackNotify',{type:'error',msg:errText})

                    } else {
                        console.log(err.response);
                        this.$store.commit('snackNotify',{type:'error',msg:'Something Went Wrong'})
                        this.Actiondialog = false;
                        this.$emit('reset');
                    }

                })

        },
        filter(id){
            
            const f=id;
            
            this.$emit('filter',f)
        }

    },
    computed: {
        emailErrors() {
            const errors = [];
            if (!this.$v.email.$dirty) return errors;
            !this.$v.email.email && errors.push('Email Provided is invalid');
            !this.$v.email.required && errors.push('Email is required.');
            return errors
        },
        actionErrors() {
            const errors = [];
            if (!this.$v.action.$dirty) return errors;
            !this.$v.action.maxLength && errors.push('Action must be at most 400 characters long');
            !this.$v.action.required && errors.push('Action is required.');
            return errors
        },
        personErrors() {
            const errors = [];
            if (!this.$v.person.$dirty) return errors;
            !this.$v.person.maxLength && errors.push('Person name must be at most 30 characters long');
            !this.$v.person.required && errors.push('Person Name is required.');
            return errors
        },
        targetErrors() {
            const errors = [];
            if (!this.$v.target.$dirty) return errors;
            !this.$v.target.required && errors.push('Target Date is required.');
            return errors
        }
    },
    created() {
        //console.log(this.item_id);
    }
}
</script>
