<script>
//import VueApexCharts from 'vue-apexcharts'

export default {
    props: {
        register: {
            type: Object,
            required: true
        }
    },
    data: () => ({

        donutOptions: {
            chart: {
                id: 'OT-Chart',
                width: "100%",
                height: 250,
            },
            labels: ["Opportunity", "Threat"],
            responsive: [{
                breakpoint: 480,
                options: {
                    chart: {
                        width: "100%"
                    },
                    legend: {
                        position: 'bottom'
                    }
                }
            }],
            plotOptions: {
                pie: {
                    donut: {
                        size: '50%',
                        labels: {
                            show: true,
                            name: {
                                show: false,
                                formatter: function (val) {
                                    return 'ITEMS'
                                }
                            },
                            value: {
                                show: true,
                                formatter: function (val) {
                                    return val;
                                }
                            }
                        }
                    }
                }
            },

            legend: {
                position: 'bottom'
            },
            dataLabels: {
                enabled: true
            },

            colors: ['#28c76f', '#ea5455']

        },
        chartOptions: {
            chart: {
                type: 'bar',
                height: "100%",
                toolbar: {
                    show: false
                }
            },
            legend: {
                show: false
            },

            responsive: [{
                breakpoint: 480,
                options: {
                    chart: {
                        width: "100%"
                    },

                }
            }],
            plotOptions: {
                bar: {
                    horizontal: true,
                    barHeight: '70%',
                    endingShape: 'flat',
                    distributed: true,
                },

            },
            colors: ['#7367F0', '#28C76F', '#EA5455', '#FF9F43', '#0075cf'],
            dataLabels: {
                enabled: true,
                textAnchor: 'start',
                style: {
                    colors: ['#ffffff'],
                    fontSize: '14px',
                    fontFamily: 'Roboto',
                    fontWeight: 'bold',
                },
                formatter: function (val, {
                    seriesIndex,
                    dataPointIndex,
                    w
                }) {

                    return w.globals.labels[dataPointIndex] + ":  " + val
                },
                offsetX: 0,
                dropShadow: {
                    enabled: true
                }
            },
            stroke: {
                show: false,
                width: 0,
                colors: ['#fff'],

            },
            xaxis: {
                show: false,
                categories: [],
                lines: {
                    show: false,
                },
                labels: {
                    show: false,
                },
                axisBorder: {
                    show: false,
                },
                axisTicks: {
                    show: false,
                }
            },
            yaxis: {
                show: false,
                lines: {
                    show: false,
                },
                labels: {
                    show: false,
                    style: {
                        fontSize: '14px',
                        fontWeight: 600
                    }
                },
                axisBorder: {
                    show: false,
                },
                axisTicks: {
                    show: false,
                }

            },
            grid: {
                xaxis: {
                    lines: {
                        show: false
                    }
                },
                yaxis: {
                    lines: {
                        show: false
                    }
                },
            }

        },
        rbsOptions: {
            chart: {
                width: "100%",
                type: 'pie',
                height: 250,
            },
            legend: {
                show: false,
                horizontalAlign: 'left'
            },
            colors: ["#3366cc", "#dc3912", "#ff9900", "#109618", "#990099", "#0099c6", "#dd4477", "#66aa00", "#b82e2e", "#316395", "#3366cc", "#994499", "#22aa99", "#aaaa11", "#6633cc", "#e67300", "#8b0707", "#651067", "#329262", "#5574a6", "#3b3eac", "#b77322", "#16d620", "#b91383", "#f4359e", "#9c5935", "#a9c413", "#2a778d", "#668d1c", "#bea413", "#0c5922", "#743411"],
            labels: [],
            responsive: [{
                // breakpoint: 480,
                // options: {
                //     chart: {
                //         width: "100%"
                //     },
                //     legend: {
                //         position: 'bottom'
                //     }
                // }
            }]
        },

        statusChart: {
            name: [],
            count: []
        },

        rbsChart: [],
        ownerHeaders: [{
                text: 'Owner',
                align: 'start',
                sortable: false,
                value: 'name',
            },
            {
                text: 'Items',
                value: 'count'
            },
            {
                text: 'Contingency(O)',
                value: 'c_opp'
            },
            {
                text: 'Contingency(T)',
                value: 'c_threat'
            }

        ],
        ownerChart: [],
        actionVal: '',
        switchOT: false,
        donutData: ''

    }),
    components: {
        'apexChart': VueApexCharts
    },
    methods: {
        statusChartFn(n = this.registers) {
            console.log('I am called');
            this.statusChart.count = {

                data: n.dashboard.statusReport.map((x) => {
                    return Number(x.count);
                })
            }

            this.chartOptions.xaxis.categories = n.dashboard.statusReport.map((x) => {
                return x.name;
            })
            var maxVal = Math.max(...this.statusChart.count.data)
            this.chartOptions.yaxis.max = maxVal + (0.3 * maxVal);

        },
        rbsChartFn(n = this.registers) {
            this.rbsChart =
                n.dashboard.rbsReport.map((x) => {
                    return Number(x.count);
                })

            this.rbsOptions.labels = n.dashboard.rbsReport.map((x) => {
                return x.name;
            })
        },
        ownerChartFn(n = this.registers) {
            this.ownerChart = n.dashboard.ownerReport;
        },
        scMatrix(n, type = 1) {
            var arr = this.register.dashboard.scoreMatrix.threat;
            if (type == 1) {
                arr = this.register.dashboard.scoreMatrix.opp;
            }

            var res = this.occurence(arr);

            if (Object.keys(res).indexOf(n.toString()) >= 0) {

                return res[n].length;
            } else {
                return 0;
            }

        },
        occurence(array) {

            var result = {};
            if (array instanceof Array) { // Check if input is array.
                array.forEach(function (v, i) {
                    if (!result[v]) { // Initial object property creation.
                        result[v] = [i]; // Create an array for that property.
                    } else { // Same occurrences found.
                        result[v].push(i); // Fill the array.
                    }
                });
            }
            return result;
        },
        switchDonutFn() {
            if (this.switchOT) {
                var total = Number(this.register.dashboard.contigency[1]) + Number(this.register.dashboard.contigency[2])
                if (total != 0) {
                    var opp = Number(this.register.dashboard.contigency[1]) / total;
                    var th = Number(this.register.dashboard.contigency[2]) / total;

                    this.donutData = [opp, th];
                } else {
                    this.donutData = [0, 0];
                }

            } else {
                this.donutData = this.register.dashboard.typeData;
            }
        }

    },

    created() {
        this.switchDonutFn()
        this.statusChartFn(this.register)
        this.rbsChartFn(this.register)
        this.ownerChartFn(this.register)

        if (this.register.dashboard.actionReport['total'] == 0) {
            this.actionVal = 0;
        } else {
            this.actionVal = ((Number(this.register.dashboard.actionReport['comp']) / Number(this.register.dashboard.actionReport['total'])) * 100).toFixed(1);
        }
    }

}
</script>
